@use '../../../Public/SCSS/Utilities/Variables' as *;

/////////////////////////////////
// Base

html {
  font-family: Purely Me, Century Gothic, Verdana, Sans-Serif;
  font-variant-ligatures: none;
}

.text-centered {
  text-align: center;
}

// LANDING LOGO AND SLOGAN
.logo-container {
  .logo-text {
    font-size: 7rem;
    color: $primaryColor;
  }
}
.slogan {
  font-family: MADE Tommy Soft, Verdana, sans-serif;
  font-size: 3.6rem;
  color: $primaryColor;
}

// APPLICATION NAME -- LOGGED IN
.application-name {
  font-size: 7rem;
  color: $primaryColor;
}

// MODAL
.modal {
  font-family: MADE Tommy Soft, Verdana, sans-serif;
  &--update-photo {
    @extend .modal;
  }

  &--receipt {
    @extend .modal;
    color: $primaryColor;
    &__closure-icon {
      font-size: 4rem;
      color: rgba($primaryColor, 0.8);
      &:hover {
        color: $primaryColor;
      }
    }
    &__digital-receipt {
      &__container {
        &__header {
          font-family: MADE Tommy Soft Bold, Verdana, sans-serif;
          font-size: 5rem;
          &__secondary-title-container {
            font-family: MADE Tommy Soft, Verdana, sans-serif;
            font-size: 2rem;
          }
        }
        &__item-container {
          &__row {
            font-size: 1.2rem;
            .receipt-row__transaction-details-section {
              font-size: 1.2rem;
            }
          }
        }
        &__footer {
          .footer-title {
            &:first-of-type {
              font-size: 3rem;
            }
            &:nth-of-type(2) {
              font-size: 4rem;
            }
          }
        }
      }
    }
  }
}

// NAVIGATION
.navigation {
  &__link-list {
    &__list-item {
      &__link,
      &__link-button {
        font-family: MADE Tommy Soft, Verdana, sans-serif;
        font-size: 2.4rem;
        text-decoration: none;
        color: rgba($colorOffWhite, 0.8);
        &:hover {
          color: $colorOffWhite;
        }
        &:active {
          color: $colorDarkerGrey;
        }
      }
      &__sub-link-container {
        font-family: MADE Tommy Soft, Verdana, sans-serif;
        font-size: 2rem;
        &__sub-link {
          color: rgba($colorOffWhite, 0.8);
          text-decoration: none;
        }
        &:hover {
          .budget-navigation__link-lidt__link-item__sub-link-container__sub-link {
            color: $colorOffWhite;
          }
        }
        &:active {
          .budget-navigation__link-lidt__link-item__sub-link-container__sub-link {
            color: $colorDarkerGrey;
          }
        }
      }
    }
  }
  &--side-screen {
    @extend .navigation;
    &__section {
      &--user-information {
        font-family: MADE Tommy Soft, Verdana, sans-serif;
        &__update-picture {
          color: rgba($colorOffWhite, 0.8);
          &--icon {
            font-size: 1.6rem;
          }
          &--text {
            font-size: 1.6rem;
          }
          &:hover {
            .navigation--side-screen__section--user-information__update-picture--icon,
            .navigation--side-screen__section--user-information__update-picture--text {
              color: $colorOffWhite;
            }
          }
        }
        &__username {
          font-size: 3.6rem;
          color: $colorOffWhite;
        }
      }
      &--account-links {
        font-family: MADE Tommy Soft, Verdana, sans-serif;
        &__link-container {
          &__link {
            &--link {
              font-size: 2rem;
              color: rgba($colorOffWhite, 0.8);
              &:link,
              &:visited {
                text-decoration: none;
                color: rgba($colorOffWhite, 0.8);
              }
              &:hover {
                color: $colorOffWhite;
              }
              &:active {
                color: $colorDarkGrey;
              }
            }
          }
        }
      }
      &--application-logo {
        &__text {
          font-size: 3rem;
          color: $colorOffWhite;
        }
      }
    }
  }
}

// BUTTON CONTAINER
.button-container {
  font-family: MADE Tommy Soft, Verdana, sans-serif;
  &__navigation-button-container {
    @extend .button-container;
  }
}

.budget-container {
  @extend .button-container;
  &__navigation-button-container {
    @extend .budget-container;
    font-size: 2.8rem;
    color: $primaryColor;
  }
  &__header {
    &__text {
      @extend .budget-container;
      font-size: 7rem;
      color: $primaryColor;
    }
  }
}

// BUTTONS
.button {
  font-family: MADE Tommy Soft, Verdana, sans-serif;
  font-size: 2.2rem;
  color: rgba($primaryColor, 0.75);
  &:hover {
    color: $colorOffWhite;
  }

  &--update-photo {
    @extend .button;
    font-size: 1.2rem;
    color: rgba($colorOffWhite, 0.8);
    &:hover {
      color: $colorOffWhite;
    }
  }

  &--settle {
    @extend .button;
    font-size: 1.4rem;
  }

  &--confirm-value {
    @extend .button;
    font-size: 1.4rem;
    color: rgba($colorOffWhite, 0.8);
    &:hover {
      color: $colorOffWhite;
    }
  }

  &--budget-navigation {
    @extend .button;
    font-size: 2.8rem;
    color: $primaryColor;
    &--clicked {
      & ~ .budget-container__navigation-button-container__text {
        color: rgba($colorOffWhite, 0.8);
      }
    }
  }
  &--transaction-button {
    @extend .button;
    font-size: 1.4rem;
    &:hover {
      color: $colorOffWhite;
    }
  }

  &--extra-extra-small {
    @extend .button;
    font-size: 1.6rem;
    color: rgba($colorOffWhite, 0.8);
    &:hover {
      color: $colorOffWhite;
    }
    &__transaction-plan,
    &__debt-transaction-plan {
      @extend .button--extra-extra-small;
      font-size: 1.4rem;
      &-small {
        @extend .button--extra-extra-small__transaction-plan;
      }
    }
    &__view-receipt {
      @extend .button--extra-extra-small;
      font-size: 1.2rem;
    }
    &__wide {
      @extend .button--extra-extra-small;
    }
    &__wider {
      @extend .button--extra-extra-small;
    }
    &__alt {
      @extend .button--extra-extra-small;
    }
  }
  &--extra-small {
    color: rgba($colorOffWhite, 0.8);
    &--icon,
    &--text {
      font-size: 1.6rem;
    }
    &:hover {
      .app-navigation__main__user-information__update-picture--icon,
      .app-navigation__main__user-information__update-picture--text {
        color: $colorOffWhite;
      }
    }
  }
  &--small {
    @extend .button;
    &-purple {
      @extend .button--small;
      color: rgba($colorOffWhite, 0.8);
    }
    &-transparent {
      color: rgba($colorOffWhite, 0.8);
      &:hover {
        color: $colorOffWhite;
      }
      &__password-button {
        font-family: MADE Tommy Soft, Verdana, sans-serif;
        font-size: 1.6rem;
        color: rgba($colorOffWhite, 0.8);
        &:hover {
          color: $colorOffWhite;
        }
      }
    }
    &-create-main-category {
      @extend .button--small;
      font-size: 1.6rem;
    }
    &-create-sub-category {
      font-size: 1.6rem;
      color: rgba($primaryColor, 0.8);
      &:hover {
        color: $colorOffWhite;
      }
      &-close {
        @extend .button--small-create-sub-category;
      }
    }
    &-container-header {
      @extend .button--small;
      color: rgba($colorOffWhite, 0.8);
      &:hover {
        color: $colorOffWhite;
      }
      &:active {
        color: $colorDarkerGrey;
      }
    }
  }

  &--timing-button {
    &__icon {
      font-family: Icomoon;
      font-size: 4rem;
      .path1:before {
        color: $primaryColor;
      }
    }
    &__text {
      font-size: 2.2rem;
      color: rgba($primaryColor, 0.8);
    }
    &:hover {
      .button--timing-button {
        &__icon {
          .path1:before {
            color: $tertiaryColor;
          }
          .path2:before,
          .path3:before {
            color: $colorDarkerGrey;
          }
        }
        &__text {
          color: $colorOffWhite;
        }
      }
    }
    &-submit {
      @extend .button;
      color: rgba($colorOffWhite, 0.75);
    }
  }
  &--borderless {
    @extend .button;
    font-size: 1.6rem;
    &:hover {
      color: $primaryColor;
    }
    &-set-timing-button {
      font-size: 1.2rem;
      color: rgba($primaryColor, 0.8);
    }
    &-narrow {
      @extend .button--borderless;
      color: rgba($colorOffWhite, 0.8);
      &:hover {
        color: $colorOffWhite;
      }
      &__investment {
        @extend .button--borderless-narrow;
        font-size: 1.3rem;
      }
    }
  }
  &--medium {
    @extend .button;
    &-square {
      @extend .button--medium;
      font-size: 2rem;
      color: rgba($primaryColor, 0.8);
      &__icon {
        font-size: 3rem;
      }
      &:hover {
        .button--medium-square__icon,
        .button--medium-square__text {
          color: $colorOffWhite;
        }
      }
      &__icons-container {
        .icon-container {
          font-family: MADE Tommy Soft, Verdana, sans-serif !important;
          font-size: 1.6rem;
          color: $primaryColor;
          text-align: center;
          &__icon {
            font-size: 4rem;
          }
          &__text {
          }
          &:hover {
            color: $colorOffWhite;
          }
          &--clicked {
            color: $colorOffWhite;
          }
        }
      }
    }

    &-main-category-deletion {
      @extend .button--medium;
      &__icon {
        font-size: 3rem;
      }
      &__text {
        font-size: 1.2rem;
        text-align: center;
      }
      &:hover {
        color: $colorOffWhite;
      }
    }
  }

  &--large {
    @extend .button;
    &__thin {
      @extend .button--large;
      font-size: 2rem;
      color: rgba($primaryColor, 0.8);
    }
  }
}

// USER PROFILE SECTION
.user-profile-section {
  .user-profile-closure-icon {
    font-size: 4rem;
    color: rgba($colorOffWhite, 0.8);
    &:hover {
      color: $colorOffWhite;
    }
  }
  &__header {
    &__text {
      font-family: MADE Tommy Soft, Verdana, sans-serif;
      font-size: 10rem;
      color: $colorOffWhite;
    }
  }
}
////////////////////////
// BUDGET CREATION

// MAIN CATEGORY
.main-category {
  font-family: MADE Tommy Soft, Verdana, sans-serif;
  font-size: 2rem;
  color: $primaryColor;
  &__icon {
    font-size: 3rem;
  }
  &__delete {
    font-size: 1.2rem;
    color: rgba($primaryColor, 0.8);
  }
  &:hover {
    color: $colorOffWhite;
    .main-category__delete {
      color: rgba($colorOffWhite, 0.8);
      &:hover {
        color: $primaryColor;
      }
    }
  }
  &__alt {
    @extend .main-category;
    &__icon {
      font-size: 3rem;
    }
  }
}

