// SUB-CATEGORY
.sub-category {
  font-family: MADE Tommy Soft, Verdana, sans-serif;
  &-title-container {
    @extend .sub-category;
    &__title {
      // font-family: MADE Tommy Soft, Verdana, sans-serif;
      font-size: 1.6rem;
      color: $primaryColor;
    }
  }
  &-controller {
    font-size: 1.6rem;
    color: $primaryColor;
    &__icon {
      color: rgba($primaryColor, 0.8);
      font-size: 2rem;
      &:hover {
        color: $primaryColor;
      }
    }
    &__surplus-container {
      &__switch,
      &__switch__alt {
        &__toggle {
          &__icon {
            font-size: 1rem;
            color: rgba($colorOffWhite, 0.8);
          }
        }
      }
    }
  }
  &--month-view {
    &__section {
      &__category-name {
        font-size: 1.6rem;
        color: $primaryColor;
      }
      &:first-of-type {
        text-align: center;
      }
      &__input {
        font-size: 2rem;
        color: rgba($primaryColor, 0.8);
        text-align: center;
        &:focus {
          color: $primaryColor;
        }
      }
      &__content {
        font-size: 2rem;
        color: rgba($primaryColor, 0.8);
        text-align: center;
      }
    }
    &:hover {
      .sub-category--month-view__section {
        &__category-name {
          color: $colorOffWhite;
        }
        &__input {
          color: rgba($colorOffWhite, 0.8);
          &:focus {
            color: $primaryColor;
          }
        }
        &__content {
          color: $colorOffWhite;
        }
      }
      .button--borderless-set-timing-button {
        color: rgba($colorOffWhite, 0.8);
        &:hover {
          color: $colorOffWhite;
        }
      }
    }
  }
}

// BUDGET CONTAINERS
.container {
  font-family: MADE Tommy Soft, Verdana, sans-serif;

  &--budget-management {
    @extend .container;
  }
  &--extra-small {
    &__header {
      @extend .container;
      color: $colorOffWhite;
      font-size: 2.4rem;
      &__icon {
        font-size: 3rem;
      }
      &__text {
      }
    }
    &__content {
      @extend .container;
      font-size: 4rem;
      color: $primaryColor;
      &__applied-container,
      &__un-applied-container {
        @extend .container--extra-small__content;
        font-size: 1.4rem;
        &__heading {
          font-size: 1.8rem;
          text-decoration: underline;
        }
      }
      &-icon {
        @extend .container--extra-small__content;
        font-size: 8rem;
        color: rgba($primaryColor, 0.8);
      }
    }

    &__margin-left-and-right {
      @extend .container--extra-small;
      &__header {
        font-size: 2.2rem;
        color: $colorOffWhite;
        &__icon {
          font-size: 3rem;
        }
        &__investment-type {
          font-size: 1.26rem;
          color: $colorOffWhite;
        }
      }
      &__content {
        @extend .container;
        font-size: 4rem;
        color: $primaryColor;
        &-icon {
          @extend .container--extra-small__content;
          font-size: 8rem;
          color: rgba($primaryColor, 0.8);
        }
        &__column {
          .investment-explanatory-information {
            .investment-description {
              @extend .container;
              font-size: 2rem;
              color: $primaryColor;
            }
          }
          .investment-value-information,
          .investment-value-information--settled {
            @extend .container;
            &__half {
              &__header {
                font-size: 1.6rem;
                text-align: center;
                text-decoration: underline;
              }
              &__text {
                font-size: 1.4rem;
              }
            }
            &__text {
              font-size: 2.4rem;
            }
          }
        }
      }
    }
    &-evenly-spaced {
      &__header {
        @extend .container;
        color: $colorOffWhite;
        font-size: 2.4rem;
        &__icon {
          font-size: 3rem;
        }
        &__text {
        }
      }
      &__content {
        @extend .container;
        font-size: 4rem;
        color: $primaryColor;
        &__applied-container,
        &__un-applied-container {
          @extend .container--extra-small__content;
          font-size: 1.4rem;
          &__heading {
            font-size: 1.8rem;
            text-decoration: underline;
          }
        }
      }
    }
    &__row {
      &__header {
        @extend .container--extra-small__header;
        &__icon {
          @extend .container--extra-small__header__icon;
        }
      }
      &__content {
        @extend .container--extra-small__content;
      }
    }
    &:hover {
      .container--extra-small__content-icon,
      .container--extra-small__margin-left-and-right__content-icon {
        color: $primaryColor;
      }
    }
  }

  &--small {
    @extend .container;
    &__header {
      font-size: 2.4rem;
      color: $colorOffWhite;
      &__icon {
        font-size: 3rem;
      }
      &__text {
      }
    }
    &__content {
      color: $primaryColor;
      .enter-income-form,
      .enter-transaction-form {
        .form-row {
          .account-selection {
            color: rgba($primaryColor, 0.8);
            font-size: 1.4rem;
            &:hover {
              color: $colorOffWhite;
            }
            &:focus {
              border: 0.2rem solid $primaryColor;
            }
          }
          &__section {
            .form-input {
              color: rgba($primaryColor, 0.8);
              font-size: 1.4rem;
              &:focus {
                color: $primaryColor;
              }
            }
            .form-label {
              font-size: 1.4rem;
              .form-input--tithing {
                color: $colorOffWhite;
              }
            }
          }
          .form-label {
            .form-input {
              color: rgba($primaryColor, 0.8);
              font-size: 1.4rem;
              &:hover {
                color: $colorOffWhite;
              }
            }
          }
        }
      }
      .enter-transaction-form {
        .form-row {
          &:nth-of-type(1) {
            .form-button {
              color: rgba($primaryColor, 0.8);
              font-size: 1.4rem;
              &:hover {
                color: $colorOffWhite;
              }
              &:focus {
                border: 0.2rem solid $primaryColor;
              }
            }
          }
          &:nth-of-type(3) {
            .label-container {
              font-size: 1.26rem;
              text-align: left;
              select {
                font-size: 1.4rem;
              }
            }
          }
          &:nth-of-type(4) {
            .fully-paid-for {
              .paid-for-container {
                &__no,
                &__yes {
                  color: $colorOffWhite;
                }
              }
            }
          }
          &:nth-of-type(3),
          &:nth-of-type(4) {
            .form-row__section {
              &:nth-of-type(0),
              &:nth-of-type(1),
              &:nth-of-type(2),
              &:nth-of-type(3),
              &:nth-of-type(4),
              &:nth-of-type(6),
              &:nth-of-type(7),
              &:nth-of-type(9),
              &:nth-of-type(10),
              &:nth-of-type(12),
              &:nth-of-type(13) {
                width: 50%;
                .form-input {
                  width: 90%;
                }
              }
            }
          }
        }
      }
      .early-income-view {
        &__investment,
        &__savings,
        &__un-allocated {
          font-size: 3.2rem;
          color: $primaryColor;
          &__header {
            font-size: 1.2rem;
          }
        }
        &__un-allocated {
          color: $colorOffWhite;
        }
      }
    }
    &__transaction-total {
      @extend .container--small;
      font-family: MADE Tommy Soft, Verdana, sans-serif;
      font-size: 4rem;
      color: $colorOffWhite;
      &__heading {
        @extend .container--small__transaction-total;
        font-family: MADE Tommy Soft, Verdana, sans-serif;
        font-size: 3rem;
      }
    }
    &-wide {
      @extend .container--small;
      color: $colorOffWhite;
      &__upcoming-bills {
        &__titles {
          font-family: MADE Tommy Soft, Verdana, sans-serif;
          color: $colorOffWhite;
          &__title {
            font-size: 1.6rem;
          }
        }
        &__bills {
          font-family: MADE Tommy Soft, Verdana, sans-serif;
          &__bill {
            font-size: 1.6rem;
            color: rgba($primaryColor, 0.8);
            &__bill-item {
              text-align: center;
              &:nth-of-type(5) {
                input[type='checkbox'] {
                  font-size: 1.2rem;
                  &::after {
                    font-family: 'Font Awesome 5 Free';
                    font-weight: 900;
                  }
                  &:checked {
                    &:after {
                      color: $colorOffWhite;
                    }
                  }
                }
              }
            }
            &:hover {
              color: $colorOffWhite;
            }
          }
        }
      }
      &__calendar-container,
      &__upcoming-bills {
      }
    }
  }

  &--medium {
    @extend .container;
    &__half {
      @extend .container;
      &:nth-of-type(2) {
        .main-category-display {
          &__left-button,
          &__right-button {
            &__icon {
              font-size: 5rem;
              color: rgba($primaryColor, 0.8);
              &:hover {
                color: $primaryColor;
              }
            }
          }
          &__left-button {
            &__icon {
              color: rgba($primaryColor, 0.25);
            }
          }
          &__category-information {
            color: $primaryColor;
            &__icon {
              font-size: 16rem;
            }
            &__text {
              font-size: 4rem;
            }
          }
        }
      }
    }
  }

  // LARGE BUDGET CONTAINERS
  &--large {
    font-family: MADE Tommy Soft, Verdana, sans-serif;
    &__header {
      @extend .container--large;
      color: $colorOffWhite;
      &__title {
        font-size: 4.5rem;
      }
      &__icon {
        font-size: 3rem;
      }
    }

    &__transaction-title-container {
      @extend .container;
      &__transaction-title {
        font-size: 1.4rem;
        color: $colorOffWhite;
        text-align: center;
      }
    }

    &__budget-goal-summary-container {
      .budget-single-goal-summary {
        &__title {
          font-size: 1.6rem;
          color: $primaryColor;
          text-align: center;
        }
        &__amount {
          font-size: 3rem;
          color: $primaryColor;
        }
      }
    }
    &__category-container {
      .main-category-display {
        &__button-container {
          font-size: 6rem;
          .left {
            color: rgba($primaryColor, 0.25);
          }
          .right {
            color: rgba($primaryColor, 0.8);
          }
          .left,
          .right {
            &:hover {
              color: $primaryColor;
            }
          }
        }
        &__category-display {
          color: $primaryColor;
          &__icon {
            font-size: 20rem;
          }
          &__title {
            font-size: 4rem;
          }
        }
      }
      .sub-category-display {
        // &__headers {
        //   &__title-container {
        //     font-size: 1.6rem;
        //     color: $colorOffWhite;
        //     text-align: center;
        //   }
        // }
        &__sub-category {
          &__section {
            &__content {
              font-size: 1.6rem;
              color: $primaryColor;
            }
          }
          &:hover {
            .sub-category-display__sub-category__section {
              &__content {
                color: $colorOffWhite;
              }
              .button--borderless-set-timing-button {
                color: $colorOffWhite;
              }
            }
          }
        }
      }
    }
  }

  &--extra-large {
    @extend .container--large;
    color: $primaryColor;
    &__half {
      &__header {
        &__text {
          @extend .container--extra-large;
          font-size: 3.6rem;
        }
      }
    }
  }

  // Containing Element For Bill Calendar
  &-divide {
    @extend .container--small;
    color: $colorOffWhite;
    &__header {
      font-size: 2.4rem;
      &__icon {
        font-size: 3rem;
      }
    }
  }
}

// RECENT TRANSACTIONS
.recent-transaction {
  @extend .container;
  color: $primaryColor;
  &:hover {
    color: $colorOffWhite;
  }
  &__section {
    font-size: 1.2rem;
    text-align: center;

    &__header {
      font-size: 1.4rem;
      text-decoration: underline;
    }
    &__part {
      font-size: 1.2rem;
      &__header {
        font-size: 1.1rem;
        text-decoration: underline;
      }
    }
  }
}

// BUDGET GOAL SUMMARY CONTAINER
.budget-goal-summary-container {
  .budget-single-goal-summary {
    &__title {
      font-size: 1.6rem;
      color: $primaryColor;
      text-align: center;
    }
    &__amount {
      font-size: 3rem;
      color: $primaryColor;
    }
  }
}

// TRANSACTION PLANNER SURPLUS CONTAINER
.surplus-container {
  font-family: MADE Tommy Soft, Verdana, sans-serif;
  &__text {
    @extend .surplus-container;
    font-size: 1.4rem;
    color: $primaryColor;
  }
  &__switch {
    &__toggle {
      @extend .surplus-container;
      font-size: 1.2rem;
      color: $colorOffWhite;
    }
  }
}

// MAIN CATEGORY DISPLAY

.main-category-display {
  &__button-container {
    font-size: 6rem;
    .left {
      color: rgba($primaryColor, 0.25);
    }
    .right {
      color: rgba($primaryColor, 0.8);
    }
    .left,
    .right {
      &:hover {
        color: $primaryColor;
      }
    }
  }
  &__category-display {
    color: $primaryColor;
    &__icon {
      font-size: 20rem;
    }
    &__title {
      font-size: 4rem;
    }
  }
}

// SUB CATEGORY DISPLAY

.sub-category-display {
  &__headers {
    &__title-container {
      font-size: 1.2rem;
      color: $colorOffWhite;
      text-align: center;
    }
  }
  &__sub-category {
    &__section {
      &__category-name {
        font-size: 1.6rem;
        color: $primaryColor;
      }
      &:first-of-type {
        text-align: center;
      }
      &__set-category-timing-button {
        font-size: 1.2rem;
        color: rgba($primaryColor, 0.8);
      }
      &__input {
        font-size: 2rem;
        color: rgba($primaryColor, 0.8);
        text-align: center;
        &:focus {
          color: $primaryColor;
        }
      }
      .form__input--medium {
        font-size: 2rem;
        color: rgba($primaryColor, 0.8);
        text-align: center;
        &:focus {
          color: $primaryColor;
        }
      }
      &__content {
        font-size: 2rem;
        color: rgba($primaryColor, 0.8);
        text-align: center;
      }
    }
    &:hover {
      .sub-category-display__sub-category__section {
        background-color: $primaryColor;
        &__category-name {
          color: $colorOffWhite;
        }
        &__set-category-timing-button {
          color: rgba($colorOffWhite, 0.8);
          &:hover {
            color: $colorOffWhite;
          }
        }
        &__input {
          color: rgba($colorOffWhite, 0.8);
          &:focus {
            color: $primaryColor;
          }
        }
        &__content {
          color: $colorOffWhite;
        }
      }
    }
  }
  &__timing-container {
    &__close {
      font-size: 3rem;
      color: rgba($primaryColor, 0.8);
      &:hover {
        color: $primaryColor;
      }
    }
    &__monthly-container,
    &__bi-monthly-container,
    &__bi-weekly-container,
    &__weekly-container {
      &__button {
        &__icon {
          font-family: Icomoon;
          font-size: 4rem;
          .path1:before {
            color: $primaryColor;
          }
        }
        &__text {
          font-size: 2.2rem;
          color: rgba($primaryColor, 0.8);
        }
        &:hover {
          .sub-category-display__timing-container {
            &__monthly-container,
            &__bi-monthly-container,
            &__bi-weekly-container,
            &__weekly-container {
              &__button {
                &__icon {
                  .path1:before {
                    color: $tertiaryColor;
                  }
                  .path2:before,
                  .path3:before {
                    color: $colorDarkerGrey;
                  }
                }
                &__text {
                  color: $colorOffWhite;
                }
              }
            }
          }
        }
      }
      &__label {
        font-size: 1.4rem;
        color: rgba($primaryColor, 0.8);
        &:focus {
          color: $primaryColor;
        }
        &__input,
        &__select {
          font-size: 1.4rem;
          color: rgba($primaryColor, 0.8);
          &:focus {
            color: $primaryColor;
          }
        }
      }
      &__submit {
        font-size: 2rem;
        color: rgba($colorOffWhite, 0.8);
        &:hover {
          color: $colorOffWhite;
        }
      }
    }
  }
}

// BILL CALENDAR
.bill-calendar-container {
  .bill-calendar {
    &__header {
      font-size: 2.4rem;
      color: $primaryColor;
      &__icon {
        font-size: 3rem;
        color: rgba($primaryColor, 0.25);
        &--not-end {
          &:hover {
            color: $primaryColor;
          }
        }
        &:hover {
          color: $primaryColor;
        }
      }
    }
    &__weekdays {
      &__weekday {
        font-size: 1.6rem;
      }
    }
    &__days {
      &__single-day {
        font-size: 2rem;
        color: rgba($primaryColor, 0.8);
        &:hover {
          color: $colorOffWhite;
        }
        &--current-day {
          color: $colorOffWhite;
        }
      }
      .un-used-day {
        &:hover {
          background-color: $primaryColor;
        }
      }
    }
  }
}

// UPCOMING BILL CONTAINER
.upcoming-bills-container {
  &__titles {
    font-family: MADE Tommy Soft, Verdana, sans-serif;
    color: $colorOffWhite;
    &__title {
      font-size: 1.6rem;
    }
  }
  .upcoming-bills {
    font-family: MADE Tommy Soft, Verdana, sans-serif;
    &__bill {
      font-size: 1.6rem;
      color: rgba($primaryColor, 0.8);
      &__bill-item {
        text-align: center;
        &:nth-of-type(5) {
          input[type='checkbox'] {
            font-size: 1.2rem;
            &::after {
              font-family: 'Font Awesome 6 Free';
              font-weight: 900;
            }
            &:checked {
              &:after {
                color: $colorOffWhite;
              }
            }
          }
        }
      }
      &:hover {
        color: $colorOffWhite;
      }
    }
  }
}

// BUDGET CREATION CONTAINER
.budget-creation-container {
  &--sub-categories {
    &__main-category-display {
      &__left-button,
      &__right-button {
        &__icon {
          font-size: 5rem;
          color: rgba($primaryColor, 0.8);
          &:hover {
            color: $primaryColor;
          }
        }
      }
      &__left-button {
        &__icon {
          color: rgba($primaryColor, 0.25);
        }
      }
      &__category-information {
        color: $primaryColor;
        &__icon {
          font-size: 16rem;
        }
        &__text {
          font-size: 4rem;
        }
      }
    }
  }
}

// MONTH CONTAINER
.month-container {
  &__header {
    font-size: 2.8rem;
    color: $colorOffWhite;
    &__title {
      font-size: 4rem;
      text-transform: uppercase;
    }
  }
  &__overall-budget-summary-container {
    &--single-summary {
      &__title {
        font-size: 1.6rem;
        color: $primaryColor;
        text-align: center;
      }
      &__amount {
        font-size: 3rem;
        color: $primaryColor;
      }
    }
  }
  &__category-container {
    .main-category-display {
      &__button-container {
        font-size: 6rem;
        .left {
          color: rgba($primaryColor, 0.25);
        }
        .right {
          color: rgba($primaryColor, 0.8);
        }
        .left,
        .right {
          &:hover {
            color: $primaryColor;
          }
        }
      }
      &__category-display {
        color: $primaryColor;
        &__icon {
          font-size: 20rem;
        }
        &__title {
          font-size: 4rem;
        }
      }
    }
    .sub-category-display {
      &__headers {
        &__title-container {
          font-size: 1.2rem;
          color: $colorOffWhite;
          text-align: center;
        }
      }
    }
  }
}

.icon-container {
  font-family: MADE Tommy Soft, Verdana, sans-serif !important;
  font-size: 1.6rem;
  color: $primaryColor;
  text-align: center;
  &__icon {
    font-size: 4rem;
  }
  &:hover {
    color: $colorOffWhite;
  }
  &--clicked {
    color: $colorOffWhite;
  }
}

// TIMING CONTAINER
.timing-container {
  &__closure-icon {
    font-size: 3rem;
    color: rgba($primaryColor, 0.8);
    &:hover {
      color: $primaryColor;
    }
  }
  &__section {
    &__label {
      font-size: 1.4rem;
      color: rgba($primaryColor, 0.8);
      &:focus {
        color: $primaryColor;
      }
      &__input,
      &__select {
        font-size: 1.4rem;
        color: rgba($primaryColor, 0.8);
        &:focus {
          color: $primaryColor;
        }
      }
    }
    &__submit {
      font-size: 2rem;
      color: rgba($colorOffWhite, 0.8);
      &:hover {
        color: $colorOffWhite;
      }
    }
  }
}

.debt,
.debt--paid {
  &:hover {
    color: $colorOffWhite;
    .form__section--debt {
      .debt-title,
      .debt-text,
      .debt--paid-title,
      .debt--paid-text {
        color: $colorOffWhite;
        transition: color 0.5s;
      }
    }
  }
}

.transaction-plan {
  font-family: MADE Tommy Soft, Verdana, sans-serif;
  color: $primaryColor;
  &:hover {
    color: $colorOffWhite;
    .transaction-plan__part,
    .transaction-plan__alt__part {
      color: $colorOffWhite;
    }
  }
  &__part {
    @extend .transaction-plan;
    font-size: 1.3rem;
    &__header {
      text-align: center;
      &__text {
        text-decoration: underline;
      }
    }
    &__text {
      font-size: 1.2rem;
      text-align: center;
    }
  }
}

.transaction-plan__alt {
  @extend .transaction-plan;
  &-double {
    @extend .transaction-plan;
    &__part {
      @extend .transaction-plan__part;
      &__header {
        @extend .transaction-plan__part__header;
        font-size: 1.2rem;
        &__text {
          @extend .transaction-plan__part__header__text;
        }
      }
      &__text {
        @extend .transaction-plan__part__text;
      }
    }
  }
  &__part {
    @extend .transaction-plan__part;
    &__header {
      @extend .transaction-plan__part__header;
      &__text {
        @extend .transaction-plan__part__header__text;
      }
    }
    &__text {
      @extend .transaction-plan__part__text;
    }
  }
}

.transaction-plan__double {
  @extend .transaction-plan;
  &__part {
    @extend .transaction-plan__part;
    &__header {
      @extend .transaction-plan__part__header;
      &__text {
        @extend .transaction-plan__part__header__text;
      }
    }
    &__text {
      @extend .transaction-plan__part__text;
    }
  }
}

// FORMS
.form {
  font-family: MADE Tommy Soft, Verdana, sans-serif;
  &-closure-icon,
  &-closure-icon__alt,
  &-closure-icon--budget-creation {
    font-size: 4rem;
    color: rgba($primaryColor, 0.5);
    &:hover {
      color: $primaryColor;
    }
  }

  &__path-preview {
    @extend .form;
    font-size: 1.6rem;
    color: $primaryColor;
  }

  &--extra-small {
    @extend .form;
    &__budget-name-display {
      @extend .form--extra-small;
      font-size: 3rem;
      color: $primaryColor;
    }
  }

  &__header {
    &__title {
      font-family: MADE Tommy Soft Bold, Verdana, sans-serif;
      font-size: 10rem;
      color: $primaryColor;
      &--medium {
        font-family: MADE Tommy Soft Bold, Verdana, sans-serif;
        color: $primaryColor;
        font-size: 6rem;
      }
    }
  }
  &__section {
    font-family: MADE Tommy Soft, Verdana, sans-serif;
    font-size: 2rem;
    color: rgba($primaryColor, 0.8);

    &--transaction-receipt {
      @extend .form__section;
      .receipt-header {
        &__text {
          font-size: 3rem;
          font-variant: small-caps;
          text-decoration: underline;
          color: $primaryColor;
        }
        &__secondary-titles {
          font-size: 1.6rem;
        }
      }
      .receipt-item-container {
        &__row {
          font-size: 1.3rem;
          &:hover {
            color: $colorOffWhite;
            .transaction-item-cost {
              .remove-transaction {
                color: rgba($colorOffWhite, 0.8);
              }
            }
          }
          .transaction-item-details {
          }
          .transaction-item-cost {
            .remove-transaction {
              color: rgba($primaryColor, 0.8);
              &:hover {
                color: $colorOffWhite;
              }
            }
          }
        }
      }
    }

    &--debt {
      @extend .form__section;
      color: $primaryColor;
      font-size: 1.6rem;
      text-align: center;
      .debt-title,
      .debt--paid-title {
        text-decoration: underline;
      }
      .debt-text,
      .debt--paid-text {
        font-size: 1.4rem;
      }
      &:hover {
        color: $colorOffWhite;
      }
      &-paid {
        @extend .form__section--debt;
      }
    }

    &--sub-section {
      @extend .form__section;
      color: rgba($colorOffWhite, 0.8);
    }
    &--switch {
      @extend .form__section;
      &__fully-paid-for {
        @extend .form__section--switch;
        font-size: 1.4rem;
        .form__label--fully-paid-for {
          .paid-for-container {
            color: $colorOffWhite;
          }
        }
        &__alt {
          @extend .form__section--switch__fully-paid-for;
        }
      }
    }
    &--page-number-display {
      @extend .form__section;
      font-size: 3rem;
      color: $primaryColor;
      &__number {
        @extend .form__section--page-number-display;
      }
    }

    &--update-budget {
      @extend .form__section;
      font-family: MADE Tommy Soft, Verdana, sans-serif;
    }

    &--emergency {
      @extend .form__section;
      &__header {
        font-size: 1.4rem;
        font-weight: 300;
        color: $primaryColor;
      }
      #lengthOfTime,
      #totalAmount {
        & + .clicked-label {
          color: $colorOffWhite;
        }
      }
    }
    &--early-income-view {
      @extend .form__section;
      &__income-view {
        @extend .form__section--early-income-view;
        font-size: 2.4rem;
        color: $primaryColor;
        &__header {
          font-size: 1.2rem;
        }
        &--purple {
          @extend .form__section--early-income-view__income-view;
          color: $colorOffWhite;
          &__header {
            font-size: 1.2rem;
          }
        }
      }
    }
    &--long {
      @extend .form__section;
      &-short {
        @extend .form__section--long;
      }
    }
  }

  // FORM INPUT CONTAINERS
  &__input-container {
    &--paid-for {
      color: $colorOffWhite;
    }
  }

  // FORM INPUTS
  &__input {
    font-family: MADE Tommy Soft, Verdana, sans-serif;
    font-size: 2rem;
    color: rgba($primaryColor, 0.8);
    &:focus {
      color: $primaryColor;
      & + label {
        color: $primaryColor;
      }
    }

    &--tithing {
      color: $colorOffWhite;
      &--tithed {
        @extend .form__input--tithing;
      }
    }

    &--investment {
      @extend .form__input;
      font-size: 1.2rem;
    }

    &--half-left {
      @extend .form__input;
      font-size: 1.6rem;
      color: rgba($primaryColor, 0.8);
      &:hover,
      &:focus {
        color: $primaryColor;
      }
    }
    &--medium,
    &--long {
      @extend .form__input;
      font-size: 1.4rem;
      &-short {
        @extend .form__input--long;
      }
      &__alt {
        @extend .form__input--enter-transaction;
        &-emergency-fund {
          @extend .form__input--enter-transaction__alt;
        }
      }
    }

    &--small {
      @extend .form__input;
      &-short {
        @extend .form__input--medium;
      }
      &-shorter {
        @extend .form__input--medium;
      }

      &-thinner {
        @extend .form__input--small;
        font-size: 1.2rem;
        color: rgba($primaryColor, 0.8);
        &:focus {
          color: $primaryColor;
          & + label {
            color: $primaryColor;
          }
        }
        &__wide {
          @extend .form__input--small-thinner;
        }
        &__wider {
          @extend .form__input--small-thinner;
        }
      }

      &-thin {
        @extend .form__input--small;
        font-size: 1.6rem;
        color: rgba($primaryColor, 0.8);
        &:focus {
          color: $primaryColor;
          & + label {
            color: $primaryColor;
          }
        }
        &__placeholder-shown {
          @extend .form__input--small-thin;
        }
      }
      &-savings-goal,
      &-investment-goal {
        @extend .form__input--small;
      }
    }

    &--transaction-plan {
      @extend .form__input--small-thin;
      font-size: 1.4rem;
      &-short {
        @extend .form__input--transaction-plan;
      }
    }

    &--dark {
      @extend .form__input;
      color: rgba($colorOffWhite, 0.8);
      &:focus {
        color: $colorOffWhite;
        & + label {
          color: $colorOffWhite;
        }
      }
      &-small {
        @extend .form__input--dark;
      }
      &-extra-small {
        @extend .form__input--dark;
        font-size: 1.6rem;
      }
    }
    &--main-category-title {
      @extend .form__input;
      font-size: 1.6rem;
    }

    &--sub-category-title {
      @extend .form__input;
      font-size: 1.6rem;
    }
    &--latter-day-saint {
      @extend .form__input;
      font-size: 3rem;
      color: $colorOffWhite;
      text-transform: capitalize;
    }
    &--comms {
      @extend .form__input;
      &--email,
      &--text {
        // font-size: 2rem;
        @extend .form__input--comms;
        color: $colorOffWhite;
        &__text {
          font-size: 1.6rem;
        }
      }
    }
    &--enter-income,
    &--enter-transaction {
      @extend .form__input;
      font-size: 1.4rem;
      &__alt {
        @extend .form__input--enter-transaction;
        &-emergency-fund {
          @extend .form__input--enter-transaction__alt;
        }
      }
    }
  }

  &__select {
    font-family: MADE Tommy Soft, Verdana, sans-serif;
    color: rgba($colorOffWhite, 0.8);
    font-size: 2rem;
    text-align: center;
    &:focus {
      background-color: $primaryColor;
      border: 0.3rem solid $primaryColor;
      color: $colorOffWhite;
    }
    option,
    &__option {
      font-family: MADE Tommy Soft, Verdana, sans-serif;
      color: $colorOffWhite;
      font-weight: 300;
    }
    &--accounts {
      @extend .form__select;
      color: rgba($primaryColor, 0.8);
      font-size: 1.4rem;
      &:hover {
        color: $colorOffWhite;
      }
      &:focus {
        border: 0.2rem solid $primaryColor;
      }
      &-short {
        @extend .form__select--accounts;
      }
    }

    &--half-right {
      @extend .form__select;
      font-size: 1.6rem;
      color: rgba($colorOffWhite, 0.8);
      &:hover {
        color: $colorOffWhite;
      }
    }
  }

  &__label-container {
    .checked {
      color: $colorOffWhite;
    }
  }

  &__label {
    font-family: MADE Tommy Soft, Verdana, sans-serif;
    font-size: 2rem;
    color: rgba($primaryColor, 0.8);

    // font-size: 1.4rem;
    // .form-input--tithing {
    //   color: $colorOffWhite;
    // }

    &--select {
      @extend .form__label;
      font-size: 1.26rem;
      &__account-transfer {
        @extend .form__label--select;
      }
      &__transaction {
        @extend .form__label--select;
      }
    }

    &--outer-checkbox {
      @extend .form__label;
      font-size: 1.1rem;
      color: $primaryColor;
    }

    &--checkbox-container {
      @extend .form__label;
      font-size: 1.4rem;
      color: $primaryColor;
    }

    &--small {
      @extend .form__label;
      color: rgba($colorOffWhite, 0.8);

      &-short {
        @extend .form__label;
        font-size: 1.4rem;
      }

      &-shorter {
        @extend .form__label;
        font-size: 1.4rem;
      }

      &-thinner {
        @extend .form__label;
        font-size: 1.2rem;
        &__transaction {
          @extend .form__label--small-thinner;
        }
        &__wide {
          @extend .form__label--small-thinner;
        }
        &__wider {
          @extend .form__label--small-thinner;
          &-alt {
            @extend .form__label--small-thinner__wider;
          }
        }
      }

      &-thin {
        @extend .form__label;
        font-size: 1.6rem;
        &__taller {
          @extend .form__label;
          &--thirds {
            @extend .form__label;
            &__tithing {
              @extend .form__label;
              &:hover {
                color: $colorOffWhite;
              }
            }
          }
        }
      }
      &-budget {
        @extend .form__label;
        font-size: 1.6rem;
      }
      &-timing {
        @extend .form__label;
        font-size: 1.6rem;
      }
      &-savings-goal,
      &-investment-goal {
        @extend .form__label;
        font-size: 1.6rem;
        color: rgba($primaryColor, 0.8);
      }
    }
    &--extra-small {
      @extend .form__label--small;
      font-size: 1.6rem;
    }
    &--latter-day-saint {
      @extend .form__label;
      font-size: 2.2rem;
      &-white {
        @extend .form__label--latter-day-saint;
        color: rgba($colorOffWhite, 0.8);
      }
    }
    &--comms {
      @extend .form__label;
      color: $colorOffWhite;
    }
    &--enter-income,
    &--enter-transaction {
      @extend .form__label;
      font-size: 1.4rem;
      &__pay-label,
      &__transaction-label {
        @extend .form__label--enter-income;
      }
      .form-input--tithing {
        color: $colorOffWhite;
      }
    }
    &--medium,
    &--long,
    &--emergency-fund-transaction,
    &--savings-fund-transaction,
    &--expense-fund-transaction {
      @extend .form__label;
      font-size: 1.26rem;
      text-align: left;
      select {
        @extend .form__select--accounts;
      }
      &__text {
      }
      &__alt {
        @extend .form__label--medium;
        &-2 {
          @extend .form__label--medium;
        }
        &-3 {
          @extend .form__label--medium;
        }
        &-4 {
          @extend .form__label--medium;
        }
      }
      &__relative {
        @extend .form__label--medium;
      }
    }
    &--fully-paid-for {
      @extend .form__label;
      font-size: 1.4rem;
    }

    &--long {
      &-short {
        @extend .form__label--long;
        font-size: 1.4rem;
      }
    }

    &--photo-upload {
      font-family: MADE Tommy Soft, Verdana, sans-serif;
      color: rgba($primaryColor, 0.8);
      &__icon,
      &__text {
        font-size: 1.6rem;
      }
      &:hover {
        color: $primaryColor;
        .app-navigation__main__user-information__update-picture__icon,
        .app-navigation__main__user-information__update-picture__text {
          color: $primaryColor;
        }
      }
    }
  }

  &__page {
    &-number {
      font-size: 2rem;
      color: $primaryColor;
    }
  }
}
.switch {
  &-container--latter-day-saint {
    .switch--latter-day-saint {
      &__text--NO,
      &__text--YES {
        font-size: 3rem;
        color: $colorOffWhite;
        text-transform: capitalize;
      }
    }
  }
  &--comms {
    &--email,
    &--text {
      font-size: 2rem;
      color: $colorOffWhite;
      &__text {
        font-size: 1.6rem;
      }
    }
  }
}

// BUDGET CARDS
.budget-card-container {
  &__card {
    font-family: MADE Tommy Soft, Verdana, sans-serif;
    &__header {
      &__text {
        font-size: 2.4rem;
        font-family: Purely Me;
        color: rgba($colorOffWhite, 0.8);
      }
      &__icon {
        font-size: 2rem;
        color: rgba($colorOffWhite, 0.8);
      }
    }
    &__content {
      &__change-photo-button {
        &__icon {
          font-size: 1.6rem;
          color: rgba($primaryColor, 0.5);
        }
      }
    }
    &__footer {
      color: rgba($colorOffWhite, 0.8);
      &--top {
        font-size: 1.8rem;
      }
      &--bottom {
        font-size: 1.8rem;
      }
    }
    &:hover {
      .budget-card-container__card__header {
        &__text {
          color: $colorOffWhite;
        }
        &__icon {
          color: $colorOffWhite;
          &:hover {
            color: $secondaryColor;
          }
        }
      }
      .budget-card-container__card__content {
        &__change-photo-button {
          &:hover {
            .budget-card-container__card__content__change-photo-button__icon {
              color: $colorOffWhite;
            }
          }
        }
      }
      .budget-card-container__card__footer {
        color: $colorOffWhite;
      }
    }
    &--create {
      &__header {
        &__text {
          font-size: 2rem;
          color: rgba($colorOffWhite, 0.8);
        }
      }
      &__icon {
        font-size: 10rem;
        color: rgba($primaryColor, 0.8);
      }
      &:hover {
        .budget-card-container__card--create__header {
          &__text {
            color: $colorOffWhite;
          }
        }
        .budget-card-container__card--create__icon {
          color: $primaryColor;
        }
      }
    }
  }
}

.positive-investment {
  border-color: $secondaryColor;
  .container--extra-small__margin-left-and-right__header {
    background-color: $secondaryColor;
  }
  .container--extra-small__margin-left-and-right__content__column {
    .investment-explanatory-information {
      .investment-description {
        &__text {
          color: $secondaryColor;
        }
      }
    }
    .investment-value-information--settled {
      &__text {
        color: $secondaryColor;
      }
    }
  }
}
.neutral-investment {
  border-color: $primaryColor;
  .container--extra-small__margin-left-and-right__header {
    background-color: $primaryColor;
  }
  .container--extra-small__margin-left-and-right__content__column {
    .investment-explanatory-information {
      .investment-description {
        &__text {
          color: $primaryColor;
        }
      }
    }
    .investment-value-information--settled {
      &__text {
        color: $primaryColor;
      }
    }
  }
}
.negative-investment {
  border-color: $colorError;
  .container--extra-small__margin-left-and-right__header {
    background-color: $colorError;
  }
  .container--extra-small__margin-left-and-right__content__column {
    .investment-explanatory-information {
      .investment-description {
        &__text {
          color: $colorError;
        }
      }
    }
    .investment-value-information--settled {
      &__text {
        color: $colorError;
      }
    }
  }
}

.postitive {
  &-centered {
    @extend .text-centered;
    color: $secondaryColor;
  }
  color: $secondaryColor;
}
.negative {
  &-centered {
    @extend .text-centered;
    color: $colorError;
  }
  color: $colorError;
}
