@use '../../../Public/SCSS/Utilities/Mixins' as *;
@use '../../../Public/SCSS/Utilities/Variables' as *;

///////////////////////////////////
// TRANSACTION DISPLAY
.transaction-plan-display {
  @include SizeAndPositioning(relative, 90%, 100%, flex, column, nowrap, flex-start, center, 0, 0, 0);
  overflow-y: auto;
}

.debt-display,
.debt-display--paid {
  @extend .transaction-plan-display;
  height: 50%;
  justify-content: flex-start;
}

.transaction-plan-creation {
  @include SizeAndPositioning(relative, 9rem, 100%, flex, row, wrap, space-evenly, center, 0, 0, 0);
  min-height: 9rem;
  border-bottom: 0.2rem solid $primaryColor;
}

.add-debt-container {
  @extend .transaction-plan-creation;
  justify-content: space-evenly;
}

.transaction-plan,
.transaction-plan__alt {
  // Every Other Account {
  @extend .transaction-plan-creation;
  justify-content: space-between;
  height: 10rem;
  &:nth-of-type(2n) {
    background-color: $colorLightestGrey;
  }
  &:hover {
    cursor: pointer;
    background-color: $primaryColor;

    .button--extra-extra-small__transaction-plan-small {
      border: 0.2rem solid rgba($colorOffWhite, 0.8);
      &:hover {
        background-color: $secondaryColor;
      }
    }

    transition: color 0.5s, background-color 0.5s;
  }
  &__part {
    @include SizeAndPositioning(relative, 100%, calc(100% / 12), flex, column, nowrap, space-evenly, center, 0, 0, 0);
    padding: 0.5rem;
    // &:not(:last-of-type) {
    //   border-right: 0.2rem solid $primaryColor;
    // }
    &__header {
      @include SizeAndPositioning(relative, 3rem, 100%, flex, row, wrap, center, center, 0, 0, 0);
    }
  }
}

.transaction-plan__double {
  @extend .transaction-plan;
  &__part {
    @extend .transaction-plan__part;
    width: calc(100% / 13);
  }
}

.transaction-plan__alt {
  &-double {
    @extend .transaction-plan;
    &__part {
      @extend .transaction-plan__part;
      width: calc(100% / 14);
      &__header {
        @extend .transaction-plan__part__header;
      }
    }
  }
  &__part {
    @extend .transaction-plan__part;
    width: calc(100% / 13);
    &__header {
      @extend .transaction-plan__part__header;
    }
  }
}

///////////////////////////////////
// FORM COMPONENTS
.form {
  @include SizeAndPositioning(relative, 100%, 50%, flex, column, nowrap, center, center, 0, 0, 0);
  overflow-y: auto;

  &__path-preview {
    @include SizeAndPositioning(relative, 4rem, 20%, flex, row, nowrap, center, center, 0, 0, 0);
    border: 0.2rem solid $primaryColor;
    border-radius: 0.5rem;
    padding: 0.3rem;
    background-color: rgba($colorOffWhite, 0.8);
    margin: 0 1rem 0 0;
  }

  &__preview-photo-container {
    @include SizeAndPositioning(relative, 50%, 100%, flex, column, nowrap, center, center, 0, 0, 0);
    &__picture-frame {
      @include SizeAndPositioning(relative, 40rem, 40rem, flex, row, nowrap, center, center, 0, 0, 0);
      border-radius: 50%;
      background-color: rgba($primaryColor, 0.4);
      padding: 0.5rem;
      img {
        @include SizingItem(relative, 100%, 100%);
        border-radius: 50%;
      }
    }
  }

  &-closure-icon,
  &-closure-icon__alt {
    @include SizeAndPositioning(absolute, 2rem, 2rem, flex, row, nowrap, center, center, 0, 0, 0);
    top: 5rem;
    right: 6rem;
    z-index: 10;
    &:hover {
      cursor: pointer;
      transition: color 0.5s;
    }
  }

  &-closure-icon__alt {
    top: 3rem;
  }

  &--update-photo {
    @extend .form;
  }

  &--transaction-plan {
    @extend .form;
    height: 100%;
    width: 100%;
    justify-content: space-evenly;
    overflow-y: visible;
  }

  &--framed {
    @extend .form;
    padding-top: 3rem;
    justify-content: flex-start;
    min-height: 76.8rem;
  }

  // FULL WIDTH FORMS
  &--full-width {
    @extend .form;
    width: 100%;
  }

  &--extra-small {
    @extend .form;
    height: 80%;
    width: 100%;
    &__budget-name-display {
      @include SizeAndPositioning(relative, 30%, 100%, flex, column, nowrap, center, center, 0, 0, 0);
    }
    &__row {
      @extend .form--extra-small;
      flex-flow: row wrap;
    }
    &__column {
      @extend .form--extra-small;
      height: 100%;
    }
  }

  // TRANSACTION FORMS -- INCOME / TRANSACTION
  &--transaction {
    @extend .form;
    width: 100%;
    justify-content: flex-start;
    scrollbar-color: rgba($secondaryColor, 0.8) rgba($colorLightestGrey, 0.2);
    scrollbar-width: 0.5rem;

    ::-webkit-scrollbar-track,
    ::-moz-scrollbar-track {
      background-color: rgba($colorLightestGrey, 0.8);
      border-radius: 0.5rem;
    }
    ::-webkit-scrollbar-thumb {
      background-color: rgba($secondaryColor, 0.8);
    }
  }

  // FORM HEADERS
  &__header {
    margin-bottom: 3rem;
  }

  // FORM ROWS
  &__row {
    @include SizeAndPositioning(relative, 32.5%, 100%, flex, row, nowrap, flex-start, flex-end, 0, 0, 0);
    padding: 0 2rem 2rem;

    &--transaction-header {
      @extend .form__row;
      height: 7rem;
      min-height: 7rem;
      padding: 0;
      justify-content: space-evenly;
      align-items: center;
    }

    &--update-photo {
      @extend .form__row;
      justify-content: center;
      align-items: center;
    }
    &--transaction {
      @extend .form__row;
      height: 100%;
      padding: 0 2rem;
      flex-wrap: wrap;

      /* 
      This is the way to get the proper result right now.  It depends on if the width of the select option becomes longer than 10.5rem.
      
      If it does, then, the current row, gets a margin bottom of 9rem.  The transaction-plan-creation container will also get a height of 18rem.  If it is NOT that length, and it will adjust automatically, it will remove that margin altogether.
      
      */
    }
    &--debt {
      @extend .form__row--transaction;
      justify-content: space-evenly;
      &-creation {
        @extend .form__row--debt;
        justify-content: center;
      }
    }
    &--thirds {
      @extend .form__row;
      min-height: 33.3%;
      padding: 0 2rem;
      flex-wrap: wrap;
      overflow-y: auto;
      // align-items: center;

      /* 
        This is the way to get the proper result right now.  It depends on if the width of the select option becomes longer than 11.272rem.
        
        If it does, then, the current row, gets a margin bottom of 8rem.  If it is NOT that length, and it will adjust automatically, it will remove that margin altogether.
      */

      scrollbar-color: rgba($secondaryColor, 0.8) rgba($colorLightestGrey, 0.2);
      scrollbar-width: 0.5rem;

      ::-webkit-scrollbar-track,
      ::-moz-scrollbar-track {
        background-color: rgba($colorLightestGrey, 0.8);
        border-radius: 0.5rem;
      }
      ::-webkit-scrollbar-thumb {
        background-color: rgba($secondaryColor, 0.8);
      }

      &--extended {
        @extend .form__row--thirds;
        margin-bottom: 8rem;
      }

      &__account-transfer {
        @extend .form__row--thirds;
        justify-content: center;
        align-items: center;
        flex-direction: column;
        &__alt {
          @extend .form__row--thirds__account-transfer;
          justify-content: space-evenly;
          flex-direction: row;
        }
      }
    }
    &--transaction {
      @extend .form__row;
      flex-wrap: wrap;
    }
  }

  // FORM SECTIONS
  &__section {
    @include SizeAndPositioning(relative, min-content, 100%, flex, column, nowrap, space-evenly, center, 0, 0, 0);
    &:not(:last-of-type) {
      margin-bottom: 3.5rem;
    }

    &--account-transfer-half {
      @extend .form__section;
      height: 100%;
      width: max-content;
      &:not(:last-of-type) {
        margin-bottom: 0rem;
      }
    }

    &--transaction-receipt {
      @include SizeAndPositioning(relative, max-content, 100%, flex, column, nowrap, flex-start, center, 0, 0, 0);
      .receipt-header {
        @include SizeAndPositioning(relative, 6rem, 75%, flex, column, nowrap, space-evenly, center, 0, 0, 0);
        border-bottom: 0.2rem solid rgba($primaryColor, 0.5);
        &__secondary-titles {
          @include SizeAndPositioning(relative, max-content, 100%, flex, row, nowrap, space-between, center, 0, 0, 0);
        }
      }

      .receipt-item-container {
        @include SizeAndPositioning(relative, max-content, 80%, flex, column, nowrap, flex-start, center, 0, 0, 0);
        &__row {
          @include SizeAndPositioning(relative, 4rem, 95%, flex, row, nowrap, space-between, center, 0, 0, 0);
          min-height: 4rem;
          padding: 0 1rem;
          // &:last-of-type {
          //   margin-bottom: 3rem;
          // }
          .transaction-item-details {
            @include SizeAndPositioning(relative, 100%, 50%, flex, row, nowrap, flex-start, center, 0, 0, 0);
            &__detail {
              &:first-of-type {
                margin-right: 1rem;
              }
            }
          }
          .transaction-item-cost {
            @extend .transaction-item-details;
            justify-content: flex-end;
            .remove-transaction {
              margin-left: 1rem;
              &:hover {
                cursor: pointer;
              }
            }
          }
          &:nth-of-type(2n) {
            background-color: $colorLightestGrey;
          }
          &:hover {
            cursor: pointer;
            background-color: $primaryColor;
            transition: background-color 0.5s;
            .transaction-item-cost {
              .remove-transaction {
                transition: color 0.5s;
              }
            }
          }
        }
      }

      .receipt-footer {
        @extend .receipt-header;
        justify-content: space-around;
        border-bottom: none;
        border-top: 0.2rem solid rgba($primaryColor, 0.5);
      }
    }

    &--transaction-header {
      @extend .form__section;
      height: 100%;
      width: 30%;
      &:not(:last-of-type) {
        margin-bottom: 0rem;
      }
    }

    &--transaction-item-creation {
      @extend .form__section;
      height: 14rem;
      justify-content: flex-start;
      align-items: flex-start;
      flex-flow: row wrap;
      border-bottom: 0.2rem solid $primaryColor;
      padding: 1rem 0 1rem 1rem;
    }

    &--main-category-title {
      @extend .form__section;
      height: 20%;
      flex-direction: row;
      border-top: 0.3rem solid $primaryColor;
      &:not(:last-of-type) {
        margin-bottom: 0;
      }
    }

    // SUB CATEGORY CREATION SECTION
    &--sub-category-creation {
      @extend .form__section;
      height: 6rem;
      flex-direction: row;
      justify-content: center;
      min-height: 6rem;
      border-bottom: 0.3rem solid rgba($primaryColor, 0.8);
      border-left: none;
      border-right: none;
      background-color: transparent;
      &__sub-category {
        @include SizeAndPositioning(relative, 6rem, 100%, flex, row, nowrap, center, center, 0, 0, 0);
        .sub-category-title-container {
          @include SizeAndPositioning(relative, 100%, 70%, flex, row, nowrap, flex-start, center, 0, 0, 0);
          padding-left: 2rem;
        }
        .sub-category-controller {
          @include SizeAndPositioning(relative, 100%, 30%, flex, row, nowrap, space-evenly, center, 0, 0, 0);
          &__surplus-container {
            @include SizeAndPositioning(relative, 100%, 50%, flex, column, nowrap, center, center, 0, 0, 0);
            &__switch {
              @include SizeAndPositioning(relative, 40%, 75%, flex, row, nowrap, flex-start, center, 0, 0, 0);
              border: 0.2rem solid rgba($primaryColorDark, 0.8);
              border-radius: 2rem;
              background-color: rgba($primaryColor, 0.8);
              transition: border 0.5s, background-color 0.5s, transform 0.5s;
              &__toggle {
                @include SizeAndPositioning(relative, 93%, 33%, flex, row, nowrap, center, center, 5%, 0, 0);
                border: 0.2rem solid rgba($colorOffWhite, 0.8);
                border-radius: 50%;
                background-color: rgba($primaryColorDark, 0.8);
                transition: border 0.5s, background-color 0.5s, transform 0.5s;
              }
              &--switched {
                @include SizeAndPositioning(relative, 40%, 75%, flex, row, nowrap, flex-start, center, 0, 0, 0);
                border: 0.2rem solid rgba($secondaryColorDark, 0.8);
                border-radius: 2rem;
                background-color: rgba($secondaryColor, 0.8);
                .sub-category-controller__surplus-container__switch__toggle {
                  @include SizeAndPositioning(relative, 93%, 33%, flex, row, nowrap, center, center, 200%, 0, 0);
                  border-radius: 50%;
                  background-color: rgba($secondaryColorDark, 0.8);
                }
              }
              &:hover {
                cursor: pointer;
              }
            }
          }
          &__icon {
            &:hover {
              cursor: pointer;
              transition: color 0.5s;
            }
          }
        }
      }
      &__input-container {
        @include SizeAndPositioning(relative, 100%, 70%, flex, row, nowrap, flex-start, center, 0, 0, 0);
        padding-left: 2rem;
        &__input {
          @include SizeAndPositioning(relative, 50%, 50%, flex, row, nowrap, flex-start, center, 0, 0, 0);
          padding-left: 1rem;
          border: 0.2rem solid rgba($primaryColor, 0.8);
          background-color: rgba($colorOffWhite, 0.25);
          border-radius: 0.5rem;
          &:focus {
            border: 0.2rem solid $primaryColor;
            outline: none;
          }
        }
      }
      &__controller {
        @include SizeAndPositioning(relative, 100%, 30%, flex, row, nowrap, space-evenly, center, 0, 0, 0);
        &__surplus-container {
          @include SizeAndPositioning(relative, 100%, 50%, flex, column, nowrap, center, center, 0, 0, 0);
          &__switch {
            @include SizeAndPositioning(relative, 40%, 75%, flex, row, nowrap, flex-start, center, 0, 0, 0);
            border: 0.2rem solid rgba($primaryColorDark, 0.8);
            border-radius: 2rem;
            background-color: rgba($primaryColor, 0.8);
            transition: border 0.5s, background-color 0.5s, transform 0.5s;
            &__toggle {
              @include SizeAndPositioning(relative, 93%, 33%, flex, row, nowrap, center, center, 5%, 0, 0);
              border: 0.2rem solid rgba($colorOffWhite, 0.8);
              border-radius: 50%;
              background-color: rgba($primaryColorDark, 0.8);
              transition: border 0.5s, background-color 0.5s, transform 0.5s;
            }
            &--switched {
              @include SizeAndPositioning(relative, 40%, 75%, flex, row, nowrap, flex-start, center, 0, 0, 0);
              border: 0.2rem solid rgba($secondaryColorDark, 0.8);
              border-radius: 2rem;
              background-color: rgba($secondaryColor, 0.8);
              .category-creation__controller__surplus-container__switch__toggle {
                @include SizeAndPositioning(relative, 93%, 33%, flex, row, nowrap, center, center, 0, 0, 0);
                border-radius: 50%;
                background-color: rgba($secondaryColorDark, 0.8);
              }
            }
            &:hover {
              cursor: pointer;
              @include SizeAndPositioning(relative, 40%, 75%, flex, row, nowrap, flex-start, center, 0, 0, 0);
              border: 0.2rem solid rgba($secondaryColorDark, 0.8);
              border-radius: 2rem;
              background-color: rgba($secondaryColor, 0.8);
              .category-creation__controller__surplus-container__switch__toggle {
                @include SizeAndPositioning(relative, 93%, 33%, flex, row, nowrap, center, center, 200%, 0, 0);
                border-radius: 50%;
                background-color: rgba($secondaryColorDark, 0.8);
              }
            }
          }
        }
        &__icon {
          &:hover {
            cursor: pointer;
            transition: color 0.5s;
          }
        }
      }
      &--shown {
        display: flex;
      }
    }

    // EMERGENCY SECTION
    &--emergency {
      @extend .form__section;
      flex-direction: row;
      justify-content: center;
      &__header {
        @include SizeAndPositioning(absolute, 6rem, 27.5%, flex, row, nowrap, center, center, 0, 0, 0);
        left: calc(30% + 9.5rem);
        top: 0;
      }
      #lengthOfTime,
      #totalAmount {
        display: none;
        .emergency-checkbox-label,
        & + label {
          @include SizeAndPositioning(relative, 6rem, 12.5%, flex, row, nowrap, center, center, 0, 0, 0);
          left: 0;
          width: 12.5%;
          border-top: 0.3rem solid $primaryColor;
          border-bottom: 0.3rem solid $primaryColor;
          margin-bottom: 4rem;
          &:first-of-type {
            border-left: 0.3rem solid $primaryColor;
            border-top-left-radius: 0.9rem;
            border-bottom-left-radius: 0.9rem;
          }
          &:nth-of-type(2) {
            border-right: 0.3rem solid $primaryColor;
            border-top-right-radius: 0.9rem;
            border-bottom-right-radius: 0.9rem;
          }
          &:hover {
            cursor: pointer;
          }
        }
        & + .clicked-label {
          background-color: $primaryColor;
        }
      }
    }

    // TITHING SECTION
    &--tithing {
      @extend .form__section;
      display: flex;
      flex-direction: row;
      justify-content: center;
      #grossOption,
      #netOption,
      #surplusOption {
        display: none;
      }
      #grossOptionLabel,
      #netOptionLabel,
      #surplusOptionLabel {
        @include SizeAndPositioning(relative, 7rem, 10%, flex, row, nowrap, center, center, 0, 0, 0);
        top: 0;
        left: 0;
        &:hover {
          cursor: pointer;
          background-color: $primaryColor;
        }
      }
      #grossOptionLabel {
        border-top: 0.3rem solid rgba($primaryColor, 0.8);
        border-left: 0.3rem solid rgba($primaryColor, 0.8);
        border-bottom: 0.3rem solid rgba($primaryColor, 0.8);
        border-top-left-radius: 1.25rem;
        border-bottom-left-radius: 1.25rem;
        &:hover {
          border-top: 0.3rem solid $primaryColor;
          border-left: 0.3rem solid $primaryColor;
          border-bottom: 0.3rem solid $primaryColor;
          transition: border 0.5s, background-color 0.5s, color 0.5s;
        }
      }
      #netOptionLabel {
        border-top: 0.3rem solid rgba($primaryColor, 0.8);
        border-bottom: 0.3rem solid rgba($primaryColor, 0.8);
        &:hover {
          border-top: 0.3rem solid $primaryColor;
          border-bottom: 0.3rem solid $primaryColor;
          transition: border 0.5s, background-color 0.5s, color 0.5s;
        }
      }
      #surplusOptionLabel {
        border-top: 0.3rem solid rgba($primaryColor, 0.8);
        border-right: 0.3rem solid rgba($primaryColor, 0.8);
        border-bottom: 0.3rem solid rgba($primaryColor, 0.8);
        border-top-right-radius: 1.25rem;
        border-bottom-right-radius: 1.25rem;
        &:hover {
          border-top: 0.3rem solid $primaryColor;
          border-right: 0.3rem solid $primaryColor;
          border-bottom: 0.3rem solid $primaryColor;
          transition: border 0.5s, background-color 0.5s, color 0.5s;
        }
      }
      .checked {
        background-color: $primaryColor;
        transition: background-color 0.5s, color 0.5s;
      }
    }

    // TIMING SECTION
    &--timing {
      @extend .form__section;
      flex-direction: row;
      justify-content: center;
      #timingNumber {
        width: 12.5%;
        border-radius: 0;
        border-top-left-radius: 0.9rem;
        border-bottom-left-radius: 0.9rem;
        border-right: none;
      }
    }
    // @include SizeAndPositioning(relative, 20%, 100%, none, row, nowrap, space-evenly, center, 0, 0, 0);

    // SWITCH SECTION
    &--switch {
      @extend .form__section;
      flex-direction: row;
      justify-content: center;
      margin-bottom: 2rem;
      &__fully-paid-for {
        @extend .form__section--switch;
        flex-direction: column-reverse;
        height: 6rem;
        min-height: 6rem;
        width: 20%;
        // @include SizeAndPositioning(relative, min-content, 100%, flex, column, nowrap, space-evenly, center, 0, 0, 0);
        // @include SizeAndPositioning(relative, 100%, 33.3%, flex, column-reverse, nowrap, center, center, 0, 0, 0);
        margin-bottom: 2.1rem;
        &:not(:last-of-type),
        &:last-of-type {
          margin-bottom: 2.1rem;
        }
        &__alt {
          @extend .form__section--switch__fully-paid-for;
          margin-bottom: 2.1rem;
          &:not(:last-of-type),
          &:last-of-type {
            margin-bottom: 2.1rem;
          }
        }
      }
    }

    // UPDATE BUDGET SECTION
    &--update-budget {
      @extend .form__section;
      height: 10%;
      flex-direction: row;
      justify-content: center;
      @include SizeAndPositioning(relative, 10%, 100%, flex, row, nowrap, center, center, 0, 0, 0);
      background-color: rgba($colorLightestGrey, 0.3);
      &:not(:last-of-type) {
        margin-bottom: 0rem;
      }
    }

    // PAGE SECTION
    &--page {
      @extend .form__section;
      flex-direction: row;
      justify-content: center;
      margin-top: 3.5rem;
      &-number-display {
        @extend .form__section;
        flex-direction: row;
      }
    }

    &--small {
      @extend .form__section;
      &:not(:last-of-type) {
        margin-bottom: 0;
      }
      &-thin {
        @extend .form__section--small;
        height: 20%;
        width: 70%;
        flex-direction: row;
        justify-content: center;
        min-height: 3.24rem;
        &__taller {
          @extend .form__section--small-thin;
          height: 40%;
        }
      }
    }

    &--medium {
      @extend .form__section;
      height: 100%;
      width: 30%;
      flex-flow: column nowrap;
      justify-content: center;
      align-items: flex-start;
      margin: 0 1rem 0 0;
      &:not(:last-of-type) {
        margin: 0 1rem 0 0;
      }
      &__alt {
        @extend .form__section--medium;
        justify-content: flex-end;
      }

      &__padded {
        @extend .form__section--medium;
        &--left {
          @extend .form__section--medium;
          padding-left: 1rem;
        }
        &--topLeft {
          @extend .form__section--medium;
          padding-top: 2rem;
          // padding-left: 1rem;
        }
      }
    }

    &--long {
      @extend .form__section;
      height: 100%;
      width: 40%;
      flex-flow: column nowrap;
      justify-content: center;
      margin: 0 1rem 0 0;
      &:not(:last-of-type) {
        margin: 0 1rem 0 0;
      }

      &__padded {
        @extend .form__section--long;
        &--left {
          @extend .form__section--long;
          padding-left: 1rem;
        }
        &--topLeft {
          @extend .form__section--long;
          padding-top: 2rem;
          padding-left: 1rem;
        }
      }
      &-half {
        @extend .form__section--long;
        width: 50%;
        justify-content: center;
      }
      &-short {
        @extend .form__section--long;
        width: 100%;
        align-items: center;
      }
    }

    &--debt {
      @extend .form__section--medium;
      width: calc(100% / 7);
      align-items: center;
      margin: 0;
      &:not(:last-of-type) {
        margin: 0;
      }
      &-paid {
        @extend .form__section--debt;
        width: calc(100% / 6);
      }
    }

    &--transaction-plan {
      @extend .form__section--medium;
      width: max-content;
      &__alt {
        @extend .form__section--transaction-plan;
        width: max-content;
      }
      .surplus-container {
        &__switch {
          &:hover {
            cursor: pointer;
          }
        }
      }
    }

    &--select {
      @extend .form__section--medium;
      justify-content: flex-start;
      height: max-content;
      width: max-content;
      margin: 0 1rem 2rem 0;
      &:not(:last-of-type) {
        margin: 0 1rem 2rem 0;
      }
    }

    &--transaction-option {
      @extend .form__section--medium;
      flex-flow: column nowrap;
      height: max-content;
      width: max-content;
      justify-content: flex-start;
      margin: 0 1rem 2rem 0;
      &:not(:last-of-type) {
        margin: 0 1rem 2rem 0;
      }
    }

    // ENTER INCOME / TRANSACTION SECTIONS
    &--enter-income,
    &--enter-transaction {
      @extend .form__section;
      height: 100%;
      width: 33.3%;
      flex-direction: column-reverse;
      justify-content: center;
      // @include SizeAndPositioning(relative, min-content, 100%, flex, column, nowrap, space-evenly, center, 0, 0, 0);
      // @include SizeAndPositioning(relative, 100%, 33.3%, flex, column-reverse, nowrap, center, center, 0, 0, 0);
      &__monthly-budget,
      &__emergency-fund {
        @extend .form__section--enter-transaction;
        align-items: flex-start;
        &:not(:last-of-type) {
          margin: 0;
        }
      }
    }

    // ENTER TRANSACTION SECTION UNIQUE ELEMENTS
    &--enter-transaction {
      margin: 0;
      &:first-of-type {
        margin: 0;
      }
      &__monthly-budget,
      &__savings-fund,
      &__expense-fund {
        &__secondary {
          @extend .form__section--enter-transaction__monthly-budget;
          min-height: 4rem;
          width: 40%;
          margin-bottom: 2rem;
          &:first-of-type,
          &:last-of-type,
          &:not(:last-of-type) {
            margin-bottom: 2rem;
          }
        }
      }
      &__emergency-fund {
        &__secondary {
          @extend .form__section--enter-transaction__emergency-fund;
          min-height: 4rem;
          width: 36%;
          flex-direction: column;
          justify-content: center;
          align-items: center;
          &:first-of-type,
          &:last-of-type,
          &:not(:last-of-type) {
            margin-bottom: 0;
          }
        }
      }
    }

    // EARLY INCOME VIEW SECTION
    &--early-income-view {
      @extend .form__section;
      height: 35%;
      flex-direction: row;
      justify-content: space-between;
      // @include SizeAndPositioning(relative, min-content, 100%, flex, column, nowrap, space-evenly, center, 0, 0, 0);
      // @include SizeAndPositioning(relative, 40%, 100%, flex, row, nowrap, space-between, center, 0, 0, 0);
      &__income-view {
        @include SizeAndPositioning(relative, 100%, 33%, flex, column, nowrap, center, center, 0, 0, 0);
        border: 0.2rem solid $primaryColor;
        &:first-of-type {
          border-bottom-left-radius: 0.4rem;
        }
        &__header {
          @include SizeAndPositioning(absolute, 1.5rem, max-content, flex, row, nowrap, center, center, 0, 0, 0);
          top: 0.25rem;
          left: 0.25rem;
        }
        &--purple {
          @extend .form__section--early-income-view__income-view;
          background-color: $primaryColor;
          border: none;
          border-bottom-right-radius: 0.4rem;
          &__header {
            @include SizeAndPositioning(absolute, 1.5rem, max-content, flex, row, nowrap, center, center, 0, 0, 0);
            top: 0.25rem;
            left: 0.25rem;
          }
        }
      }
    }

    &--sub-section {
      @extend .form__section;
      &:first-of-type {
        margin: 0 0 1rem;
      }
      margin: 0.5rem 0;
    }
  }

  // FORM INPUT CONTAINER
  &__input-container {
    @include SizeAndPositioning(relative, 7rem, 20%, flex, column, nowrap, space-evenly, center, 0, 0, 0);
    &--latter-day-saint {
      @extend .form__input-container;
      width: 12%;
    }
    &--paid-for {
      @extend .form__input-container;
      height: 4rem;
      width: 100%;
      flex-direction: row;
      justify-content: space-between;
      border: 0.2rem solid $primaryColorDark;
      border-radius: 0.5rem;
      background-color: rgba($primaryColor, 0.8);
      &:before {
        @include SizeAndPositioning(absolute, 100%, 50%, flex, row, nowrap, center, center, 0, -50%, 0);
        top: 50%;
        left: 0;
        content: '';
        border-top-left-radius: 0.2rem;
        border-bottom-left-radius: 0.2rem;
        background-color: rgba($secondaryColor, 0.95);
        transition: border 0.5s, transform 0.5s;
      }
      &__no,
      &__yes {
        @include SizeAndPositioning(relative, 100%, 50%, flex, row, nowrap, center, center, 0, 0, 0);
      }
      &:hover {
        cursor: pointer;
      }
      &--clicked {
        &:before {
          transform: translate(100%, -50%);
          border-radius: 0;
          border-top-right-radius: 0.2rem;
          border-bottom-right-radius: 0.2rem;
        }
      }
    }
    &--sub-category-title {
      @extend .form__input-container;
      height: 100%;
      width: 70%;
      flex-direction: row;
      justify-content: flex-start;
      padding-left: 2rem;
    }
  }

  // FORM INPUTS
  &__input {
    @include SizeAndPositioning(relative, 6rem, 40%, flex, row, nowrap, flex-start, center, 0, 0, 0);
    padding: 1rem;
    padding-left: 2rem;
    background-color: rgba($colorLightestGrey, 0.125);
    border: 0.2rem solid rgba($primaryColor, 0.75);
    border-radius: 0.75rem;

    &:valid {
      border: 0.2rem solid rgba($secondaryColor, 0.75);
    }
    &:invalid {
      border: 0.2rem solid rgba($colorError, 0.75);
    }

    &::-webkit-input-placeholder,
    &::-moz-placeholder,
    &::placeholder {
      opacity: 0;
    }
    &:focus {
      &::-webkit-input-placeholder {
        opacity: 1;
      }
    }
    &:focus {
      outline: none;
      &::-webkit-input-placeholder,
      &::-moz-placeholder,
      &::placeholder {
        opacity: 1;
      }
      &:valid {
        border: 0.2rem solid $secondaryColor;
      }
      &:invalid {
        border: 0.2rem solid $colorError;
      }
    }
    &:not(:placeholder-shown) + label,
    &:focus + label {
      transform: translate(-5.5rem, -7rem) scale(0.75);
    }

    &--tithing {
      @include SizeAndPositioning(absolute, 4rem, 75%, flex, row, nowrap, center, center, 4rem, 0, 0);
      border: 0.2rem solid $primaryColor;
      border-radius: 5rem;
      background-color: rgba($primaryColorDarker, 0.9);
      &__switch {
        @include SizeAndPositioning(absolute, 3.4rem, 3.4rem, flex, row, nowrap, flex-end, center, 0.2rem, -50%, 0);
        top: 50%;
        left: 0;
        border-radius: 5rem;
        background-color: $primaryColor;
      }
      &__text-no {
        display: flex;
      }
      &__text-yes {
        display: none;
      }
      &--tithed {
        @extend .form__input--tithing;
        background-color: $secondaryColor;
        border: 0.2rem solid $secondaryColorDark;
        transition: border 0.5s, background-color 0.5s;
        .form__input--tithing__switch {
          width: 96%;
          background-color: $secondaryColor;
          transition: color 0.5s, background-color 0.5s, width 0.5s;
        }
        .form__input--tithing__text-no {
          display: none;
        }
        .form__input--tithing__text-yes {
          display: flex;
          z-index: 2;
        }
      }
      &:hover {
        cursor: pointer;
      }
    }

    &--half-left {
      @extend .form__input;
      height: 100%;
      width: 50%;
      justify-content: center;
      border: none;
      border-top: 0.2rem solid $primaryColor;
      border-left: 0.2rem solid $primaryColor;
      border-bottom: 0.2rem solid $primaryColor;
      padding: 0 0 0 1rem;

      border-radius: 0;
      border-top-left-radius: 0.5rem;
      border-bottom-left-radius: 0.5rem;
      background-color: transparent;
      transition: background-color 0.5s, color 0.5s, border 0.5s;

      &:valid {
        border-top: 0.2rem solid rgba($secondaryColor, 0.75);
        border-left: 0.2rem solid rgba($secondaryColor, 0.75);
        border-bottom: 0.2rem solid rgba($secondaryColor, 0.75);
      }
      &:invalid {
        border-top: 0.2rem solid rgba($colorError, 0.75);
        border-left: 0.2rem solid rgba($colorError, 0.75);
        border-bottom: 0.2rem solid rgba($colorError, 0.75);
      }

      &:focus {
        background-color: rgba($colorOffWhite, 0.25);
        outline: none;
        &:valid {
          border-top: 0.2rem solid $secondaryColor;
          border-left: 0.2rem solid $secondaryColor;
          border-bottom: 0.2rem solid $secondaryColor;
        }
        &:invalid {
          border-top: 0.2rem solid $colorError;
          border-left: 0.2rem solid $colorError;
          border-bottom: 0.2rem solid $colorError;
        }
      }
      &:hover {
        cursor: pointer;
      }
    }

    // DARK FORM INPUTS
    &--dark {
      @extend .form__input;
      background-color: rgba($colorDarkestGrey, 0.5);
      border: 0.3rem solid rgba($colorOffWhite, 0.75);
      margin: 2rem 0;

      // SMALL DARK INPUTS
      &-small {
        @extend .form__input--dark;
        width: 25%;
        &:not(:placeholder-shown) + label,
        &:focus + label {
          transform: translate(-6rem, -7.25rem) scale(0.75);
        }
      }

      // EXTRA SMALL DARK INPUTS
      &-extra-small {
        @extend .form__input--dark;
        width: 17.5%;
        transform: translate(15%, 0);
        margin: 0.5rem 0;
        &:not(:placeholder-shown) + label,
        &:focus + label {
          transform: translate(-4rem, -7rem) scale(0.75);
        }
      }
    }

    // LONG INPUTS
    &--long {
      @extend .form__input;
      height: 4rem;
      min-height: 4rem;
      width: 100%;
      padding-left: 2rem;
      padding: 0.5rem;
      border: 0.2rem solid rgba($primaryColor, 0.8);
      border-radius: 0.9rem;

      &:not(:placeholder-shown) + .form__label--long,
      &:focus + .form__label--long {
        transform: translate(-0.5rem, -1rem) scale(0.95);
      }

      &:not(:placeholder-shown) + .form__label--long__alt,
      &:focus + .form__label--long__alt {
        transform: translate(-0.5rem, 0rem) scale(0.95);
      }

      &-short {
        @extend .form__input--long;
        height: 3.5rem;
        min-height: 3.5rem;
        &:not(:placeholder-shown) + .form__label--long-short,
        &:focus + .form__label--long-short {
          transform: translate(-4.5rem, -1.5rem) scale(0.75);
        }
      }
    }

    // MEDIUM INPUTS

    &--medium {
      @extend .form__input;
      height: 4rem;
      min-height: 4rem;
      width: 90%;
      padding-left: 2rem;
      padding: 0.5rem;
      border: 0.2rem solid rgba($primaryColor, 0.8);
      border-radius: 0.9rem;

      &:not(:placeholder-shown) + .form__label--medium,
      &:focus + .form__label--medium {
        transform: translate(-1.5rem, -1rem) scale(0.95);
      }

      &:not(:placeholder-shown) + .form__label--medium__alt-2,
      &:focus + .form__label--medium__alt-2 {
        transform: translate(-1.5rem, 0rem) scale(0.95);
      }

      &:not(:placeholder-shown) + .form__label--medium__alt-3,
      &:focus + .form__label--medium__alt-3 {
        transform: translate(-1.5rem, -0.5rem) scale(0.95);
      }

      &:not(:placeholder-shown) + .form__label--medium__alt-4,
      &:focus + .form__label--medium__alt-4 {
        transform: translate(-1.5rem, -0.5rem) scale(0.95);
      }
      &__alt {
        @extend .form__input--medium;
        width: 100%;

        &:not(:placeholder-shown) + .form__label--medium,
        &:focus + .form__label--medium {
          transform: translate(-1.5rem, 1rem) scale(0.95);
        }

        &:not(:placeholder-shown) + .form__label--medium__alt-3,
        &:focus + .form__label--medium__alt-3 {
          transform: translate(-1.5rem, -0.5rem) scale(0.95);
        }

        &:not(:placeholder-shown) + .form__label--medium__alt-4,
        &:focus + .form__label--medium__alt-4 {
          transform: translate(-1.5rem, -0.5rem) scale(0.95);
        }
      }
    }

    // SMALL INPUTS
    &--small {
      @extend .form__input;
      width: 25%;

      &-short {
        @extend .form__input--medium;
        height: 4rem;
        min-height: 4rem;
        width: 12.5rem;

        &:not(:placeholder-shown) + .form__label--small-short,
        &:focus + .form__label--small-short {
          transform: translate(-1rem, -4.5rem) scale(0.85);
        }
      }
      &-shorter {
        @extend .form__input--medium;
        height: 3.5rem;
        min-height: 3.5rem;
        width: 110%;
        align-self: flex-start;

        &:not(:placeholder-shown) + .form__label--small-shorter,
        &:focus + .form__label--small-shorter {
          transform: translate(-1rem, -4.25rem) scale(0.85);
        }
      }
      &-thinner {
        @extend .form__input--small;
        height: 3rem;
        width: 15rem;
        padding: 0.2rem;

        &:not(:placeholder-shown) + .form__label--small-thinner,
        &:focus + .form__label--small-thinner {
          transform: translate(-5.5rem, -4rem) scale(0.9);
        }

        &:not(:placeholder-shown) + .form__label--small-thinner__transaction,
        &:focus + .form__label--small-thinner__transaction {
          transform: translate(-1rem, -3.75rem) scale(0.9);
        }

        &:not(:placeholder-shown) + .form__label--small-thinner__wider-alt,
        &:focus + .form__label--small-thinner__wider-alt {
          transform: translate(-1rem, -1rem) scale(0.9);
        }

        &__wide {
          @extend .form__input--small-thinner;
          width: 85%;

          &:not(:placeholder-shown) + .form__label--small-thinner__wide,
          &:focus + .form__label--small-thinner__wide {
            transform: translate(-1rem, -4rem) scale(0.9);
          }
        }

        &__wider {
          @extend .form__input--small-thinner;
          width: 15rem;
          transform: translate(0, 0);

          &:not(:placeholder-shown) + .form__label--small-thinner__wider,
          &:focus + .form__label--small-thinner__wider {
            transform: translate(-1rem, -2.25rem) scale(0.9);
          }
        }
      }
      &-thin {
        @extend .form__input--small;
        height: 20%;
        width: 70%;
        padding: 1rem;
        border: 0.2rem solid rgba($primaryColor, 0.8);
        border-radius: 0.5rem;

        &:not(:placeholder-shown) + .form__label--small-thin,
        &:focus + .form__label--small-thin {
          transform: translate(-0.75rem, -4.25rem) scale(0.95);
        }

        &:nth-of-type(1) {
          margin-bottom: 2rem;
        }

        &__placeholder-shown {
          @extend .form__input--small-thin;
          ::-webkit-input-placeholder,
          &::placeholder,
          &::-moz-placeholder {
            opacity: 0.5;
          }
          &:nth-of-type(1) {
            margin-bottom: 0;
          }
        }
      }
      &-savings-goal,
      &-investment-goal {
        @extend .form__input--small;
        &:first-of-type {
          margin-bottom: 2.4rem;
          & + label {
            top: 21%;
          }
        }
        &:last-of-type {
          & + label {
            top: 79%;
          }
          & + .emergency-checkbox-label,
          & + .emergency-goal-label {
            top: 50%;
          }
        }
      }
    }

    &--investment {
      @extend .form__input--small-thin;
      margin-bottom: 0;
      border-top-right-radius: 0;
      border-bottom-right-radius: 0;
      border-right: none;
      &:nth-last-of-type(1) {
        margin-bottom: 0;
      }
      width: 50%;
    }

    &--transaction-plan {
      @extend .form__input--small-short;
      height: 3rem;
      min-height: 3rem;
      width: 8rem;
      background-color: rgba($colorOffWhite, 0.5);
      &:focus {
        background-color: $colorOffWhite;
      }
    }

    // INVISIBLE INPUTS
    &--invisible-checkbox {
      appearance: none;
      -webkit-appearance: none;
      display: none;
    }

    // MAIN CATEGORY TITLE INPUT
    &--main-category-title {
      @extend .form__input;
      width: 35%;
      justify-content: center;
      border-radius: 1.25rem;
      background-color: rgba($colorOffWhite, 0.8);
      &:focus {
        outline: none;
      }
    }

    // SUB CATEGORY TITLE INPUT
    &--sub-category-title {
      @extend .form__input;
      height: 50%;
      width: 50%;
      padding: 0;
      padding-left: 1rem;
      border: 0.2rem solid rgba($primaryColor, 0.8);
      background-color: rgba($colorOffWhite, 0.25);
      border-radius: 0.5rem;

      &:focus {
        border: 0.2rem solid $primaryColor;
        outline: none;
      }
    }

    // COMMUNICATIONS INPUT
    &--comms {
      @extend .form__input;
      height: 100%;
      width: 15%;
      justify-content: center;
      background-color: rgba($colorDarkestGrey, 0.5);
      border: 0.4rem solid rgba($colorOffWhite, 0.8);
      border-radius: 8rem;
      padding: 0.5rem;
      &--email,
      &--text {
        @include SizeAndPositioning(relative, 100%, 50%, flex, row, nowrap, space-evenly, center, 0, 0, 0);
        z-index: 1;
      }
      &--email {
        border-top-left-radius: 8rem;
        border-bottom-left-radius: 8rem;
      }
      &--text {
        border-top-right-radius: 8rem;
        border-bottom-right-radius: 8rem;
      }
      &:after {
        @include SizeAndPositioning(absolute, 85%, 50%, flex, row, nowrap, center, center, 0, -50%, 0);
        top: 50%;
        left: 0.5rem;
        background-color: $secondaryColor;
        border-top-left-radius: 8rem;
        border-bottom-left-radius: 8rem;
        z-index: 0;
        content: '';
        transition: transform 0.5s, border-radius 0.5s;
      }

      // TEXT PREFERRED COMMUNICATIONS INPUT
      &--text-preferred {
        @extend .form__input--comms;
        &:after {
          @include SizeAndPositioning(absolute, 85%, 50%, flex, row, nowrap, center, center, 91%, -50%, 0);
          top: 50%;
          left: 0.5rem;
          background-color: $secondaryColor;
          border-top-left-radius: 0;
          border-bottom-left-radius: 0;
          border-top-right-radius: 8rem;
          border-bottom-right-radius: 8rem;
          z-index: 0;
          content: '';
        }
      }
      &:hover {
        cursor: pointer;
      }
    }

    // LATTER DAY SAINT INPUT
    &--latter-day-saint {
      @include SizeAndPositioning(relative, 95%, 100%, flex, row, nowrap, center, center, -15%, 0, 0);
      background-color: rgba($primaryColorLight, 0.8);
      border: 0.3rem solid rgba($primaryColor, 0.75);
      border-radius: 5rem;
      padding: 0.25rem;
      transition: background-color 0.5s, border 0.5s;
      &__switch {
        @include SizeAndPositioning(absolute, 84%, 30%, flex, row, nowrap, center, center, 0, -50%, 0);
        top: 50%;
        left: 2.75%;
        background-color: rgba($primaryColorDark, 0.8);
        border-radius: 5rem;
        transition: background-color 0.5s, transform 0.5s, width 0.25s;
      }
      &__text {
        @include SizeAndPositioning(relative, 100%, 70%, flex, row, nowrap, center, center, 0, 0, 0);
      }
      &:hover {
        cursor: pointer;
      }
      &--switched {
        @extend .form__input--latter-day-saint;
        border: 0.3rem solid $secondaryColorDark;
        background-color: $secondaryColorLight;
        padding: 0.25rem;
        .form__input--latter-day-saint__switch {
          width: 94%;
          background-color: $secondaryColorDark;
          transform: translate(0, -49%);
        }

        // OLD WAY OF CHANGING WHICH IS SHOWING.  NEW WAY WILL TOGGLE THE 'CLOSED' CLASS ON BOTH OF THEM.
        // .form__input--latter-day-saint__text--NO {
        //   display: none;
        // }
        // .form__input--latter-day-saint__text--YES {
        //   display: flex;
        // }
      }
    }

    // ENTER INCOME / TRANSACTION INPUTS
    &--enter-income,
    &--enter-transaction {
      @extend .form__input;
      height: 4rem;
      min-height: 4rem;
      width: 100%;
      padding-left: 2rem;
      padding: 0.5rem;
      border: 0.2rem solid rgba($primaryColor, 0.8);
      border-radius: 0.9rem;

      &:not(:placeholder-shown) + label,
      &:focus + label {
        transform: translate(-2.25rem, -5rem) scale(0.95);
      }
      &:not(:placeholder-shown) + .form__label--enter-income__pay-label,
      &:focus + .form__label--enter-income__pay-label {
        transform: translate(-2.25rem, -7.75rem) scale(0.95);
      }
      &:not(:placeholder-shown) + .form__label--enter-transaction__transaction-label,
      &:focus + .form__label--enter-transaction__transaction-label {
        transform: translate(-2.25rem, -3rem) scale(0.95);
      }

      &:not(:placeholder-shown) + .form__label--medium,
      &:focus + .form__label--medium {
        transform: translate(-1rem, 0.75rem) scale(0.95);
      }

      &:not(:placeholder-shown) + .form__label--long,
      &:focus + .form__label--long {
        transform: translate(-1rem, 0.75rem) scale(0.95);
      }

      &:not(:placeholder-shown) + .form__label--emergency-fund-transaction,
      &:focus + .form__label--emergency-fund-transaction {
        transform: translate(-0.25rem, -5rem) scale(0.95);
      }
      transition: border 0.5s, color 0.5s;
      &__alt {
        @extend .form__input--enter-transaction;
        &-emergency-fund {
          @extend .form__input--enter-transaction__alt;
        }
        // width: max-content;
      }
    }

    &--photo-upload {
      @include SizeAndPositioning(absolute, 7.5%, 40%, flex, row, nowrap, center, center, 0, 0, 0);
      visibility: hidden;
      background-color: transparent;
      border: none;
      border-radius: 1.5rem;

      &--icon {
        margin-right: 1rem;
      }

      &:hover {
        cursor: pointer;
        background-color: transparent;
        border: none;
        transition: background-color 0.5s, border 0.5s, color 0.5s;
      }
    }
  }

  // FORM SELECTS
  &__select {
    @include SizeAndPositioning(relative, 6rem, 12.5%, flex, row, nowrap, center, center, 0, 0, 0);
    background-color: rgba($primaryColor, 0.8);
    border: 0.3rem solid rgba($primaryColor, 0.8);
    border-top-right-radius: 0.9rem;
    border-bottom-right-radius: 0.9rem;

    &--accounts {
      @extend .form__select;
      height: 3rem;
      width: max-content;
      padding: 0.25rem;
