  // FORM LABEL CONTAINERS
  &__label-container {
    @include SizeAndPositioning(relative, 30%, 70%, flex, row, nowrap, center, center, 0, 0, 0);
    min-height: 4.725rem;
    .checked {
      background-color: $primaryColor;
    }
  }
  // FORM LABELS
  &__label {
    @include SizeAndPositioning(absolute, 6rem, 40%, flex, row, nowrap, flex-start, center, 0, -50%, 0);
    top: 50%;
    left: calc(30% + 2rem);
