    transition: transform 0.5s;

    &--select {
      @extend .form__label;
      position: relative;
      width: max-content;
      height: max-content;
      top: 0;
      left: 0;
      transform: translate(1rem, 0rem);
      &__account-transfer {
        @extend .form__label--select;
        transform: translate(-2rem, 0rem);
      }
      &__transaction {
        @extend .form__label--select;
        width: max-content;
      }
    }

    &--outer-checkbox {
      @extend .form__label;
      position: relative;
      height: 20%;
      width: 75%;
      transform: translate(0, 0);
      top: 0;
      left: 0;
      padding-left: 1.5rem;
      min-height: 3.24rem;
    }

    &--checkbox-container {
      @extend .form__label;
      position: relative;
      height: 100%;
      width: 50%;
      justify-content: center;
      transform: translate(0, 0);
      top: 0;
      left: 0;
      border-top: 0.2rem solid $primaryColor;
      border-bottom: 0.2rem solid $primaryColor;
      margin-bottom: 1rem;
      transition: background-color 0.5s, color 0.5s;
      &:nth-of-type(1) {
        border-top-left-radius: 0.5rem;
        border-bottom-left-radius: 0.5rem;
        border-left: 0.2rem solid $primaryColor;
      }
      &:nth-of-type(2) {
        border-top-right-radius: 0.5rem;
        border-bottom-right-radius: 0.5rem;
        border-right: 0.2rem solid $primaryColor;
      }
      &:hover {
        cursor: pointer;
      }
    }

    &--medium,
    &--long {
      @extend .form__label;
      position: absolute;
      height: 4rem;
      width: 100%;
      top: 0;
      margin-bottom: 0.25rem;
      transform: translate(0, 0);
      left: calc(0% + 2rem);
      transition: transform 0.5s;
      -moz-transform: translate(-1rem, 2rem);

      .label-container {
        @include SizeAndPositioning(relative, 100%, 33.3%, flex, column, nowrap, center, center, 0, -0.7rem, 0);
        &__text {
          align-self: flex-start;
          transform: translateX(2rem);
        }
      }
    }

    &--medium {
      &__alt {
        @extend .form__label--medium;
        -moz-transform: translate(-1rem, 4rem);
        &-2 {
          @extend .form__label--medium;
          -moz-transform: translate(-1rem, 3rem);
        }
        &-3 {
          @extend .form__label--medium;
          -moz-transform: translate(-1rem, 3rem);
        }
        &-4 {
          @extend .form__label--medium;
          -moz-transform: translate(-1rem, 2.4rem);
          width: 12.5rem;
        }
      }

      &__relative {
        @extend .form__label--medium;
        position: relative;
        height: 100%;
        width: max-content;
        flex-direction: column;
        margin-right: 1.5rem;
        -moz-transform: translate(0, 0rem);
        &__text {
          transform: translate(0rem, 0.5rem);
          margin-bottom: 0.5rem;
        }
      }
    }

    &--long {
      -moz-transform: translate(0rem, 2rem);
      &-short {
        @extend .form__label--long;
        -moz-transform: translate(-1rem, 0.9rem);
      }
      &__alt {
        @extend .form__label--long;
        -moz-transform: translate(0rem, 3rem);
      }
    }

    &--emergency-fund-transaction {
      @extend .form__label--medium;
      position: absolute;
      top: 50%;
      left: 2.5rem;
      transform: translate(0, -50%);
      width: 85%;
      z-index: 1;
    }

    &--savings-fund-transaction,
    &--expense-fund-transaction {
      @extend .form__label--medium;
      &__alt {
        @extend .form__label--medium__alt;
        -moz-transform: translate(0, -0.3rem);
        &__text {
          margin-bottom: 0.5rem;
        }
      }
    }

    &--fully-paid-for {
      @extend .form__label;
      height: 100%;
      width: max-content;
      flex-direction: column;
      justify-content: center;
      align-items: flex-start;
      // transform: translate(0, calc(-50% + -0.9rem)), rotate(0);
      top: 57.5%;
      left: 0;
      transition: transform 0.5s;
      .paid-for-container {
        @include SizeAndPositioning(relative, 4rem, 100%, flex, row, nowrap, space-between, center, 0, 0, 0);
        border: 0.2rem solid $primaryColorDark;
        border-radius: 0.5rem;
        background-color: rgba($primaryColor, 0.8);
        &:before {
          @include SizeAndPositioning(absolute, 100%, 50%, flex, row, nowrap, center, center, 0, -50%, 0);
          top: 50%;
          left: 0;
          content: '';
          border-top-left-radius: 0.2rem;
          border-bottom-left-radius: 0.2rem;
          background-color: rgba($secondaryColor, 0.95);
          transition: border 0.5s, transform 0.5s;
        }
        &__no,
        &__yes {
          @include SizeAndPositioning(relative, 100%, 50%, flex, row, nowrap, center, center, 0, 0, 0);
        }
        &:hover {
          cursor: pointer;
        }
        &--clicked {
          &:before {
            transform: translate(4.8rem, -50%);
            border-radius: 0;
            border-top-right-radius: 0.2rem;
            border-bottom-right-radius: 0.2rem;
          }
        }
      }
    }
    // @include SizeAndPositioning(absolute, 4rem, 75%, flex, row, nowrap, flex-start, center, 0, 0, 0);
    &--enter-income,
    &--enter-transaction {
      @extend .form__label;
      height: 4rem;
      width: 95%;
      transform: translate(0, 0);
      left: calc(7% + 2rem);
      transition: transform 0.5s;
      -moz-transform: translate(-1.75rem, -2rem);
      &__pay-label {
        @extend .form__label--enter-income;
        -moz-transform: translate(-1.75rem, -4.75rem);
      }
      &__transaction-label {
        @extend .form__label--enter-income;
        -moz-transform: translate(-2.25rem, 0);
      }
      &__text {
        transform: translate(-2rem, 0);
      }
      .form-input--tithing {
        @include SizeAndPositioning(absolute, 4rem, 75%, flex, row, nowrap, center, center, 4rem, 0, 0);
        border: 0.2rem solid $primaryColor;
        border-radius: 5rem;
        background-color: rgba($primaryColorDarker, 0.9);
        &__switch {
          @include SizeAndPositioning(absolute, 3.4rem, 3.4rem, flex, row, nowrap, flex-end, center, 0.2rem, -50%, 0);
          top: 50%;
          left: 0;
          border-radius: 5rem;
          background-color: $primaryColor;
        }
        &__text-no {
          display: flex;
        }
        &__text-yes {
          display: none;
        }
        &--tithed {
          background-color: $secondaryColor;
          border: 0.2rem solid $secondaryColorDark;
          transition: border 0.5s, background-color 0.5s;
          &__switch {
            width: 96%;
            background-color: $secondaryColor;
            transition: color 0.5s, background-color 0.5s, width 0.5s;
          }
          &__text-no {
            display: none;
          }
          &__text-yes {
            display: flex;
            z-index: 2;
          }
        }
        &:hover {
          cursor: pointer;
        }
      }
    }

    &--small {
      @extend .form__label;
      width: 25%;
      left: calc(37.7% + 2rem);
      &-short {
        @extend .form__label;
        height: 3.5rem;
        min-height: 3.5rem;
        width: 100%;
        transform: translate(0rem, -1.9rem);
        left: 1rem;
      }
      &-shorter {
        @extend .form__label;
        height: 3.5rem;
        min-height: 3.5rem;
        width: 100%;
        transform: translate(0rem, -1.9rem);
        left: 1rem;
      }
      &-thinner {
        @extend .form__label--small;
        height: 3rem;
        width: 70%;
        &__transaction {
          @extend .form__label--small-thinner;
          top: 50%;
          left: 1rem;
          // transform: translate(0, 0);
          // -moz-transform: translate(0, 0);
        }
        &__wide {
          @extend .form__label--small-thinner;
          width: 85%;
          left: 2rem;
        }
        &__wider {
          @extend .form__label--small-thinner;
          width: 90%;
          top: 0;
          left: 1rem;
          transform: translate(0, 0);
          -moz-transform: translate(0, 0);
          &-alt {
            @extend .form__label--small-thinner__wider;
            transform: translate(0, 1.5rem);
            -moz-transform: translate(0, 1.5rem);
          }
        }
        // width: 25%;
        // left: calc(37.7% + 2rem);
        // @include SizeAndPositioning(absolute, 6rem, 40%, flex, row, nowrap, flex-start, center, 0, -50%, 0);
      }
      &-thin {
        @extend .form__label--small;
        @include SizeAndPositioning(absolute, 20%, 70%, flex, row, nowrap, flex-start, center, 0, -50%, 0);
        top: 56%;
        left: 6rem;
        &:first-of-type {
          top: 24%;
        }
        transition: transform 0.5s;
        &__taller {
          @extend .form__label--small-thin;
          height: 100%;
          &--thirds {
            @extend .form__label--small-thin__taller;
            top: 0;
            left: 0;
            transform: translate(0, 0);
            width: 33.3%;
            &:first-of-type {
              top: 0;
            }
            &__tithing {
              @extend .form__label--small-thin__taller--thirds;
              position: relative;
              justify-content: center;
              &:first-of-type {
                border-top: 0.2rem solid $primaryColor;
                border-left: 0.2rem solid $primaryColor;
                border-bottom: 0.2rem solid $primaryColor;
                border-top-left-radius: 0.5rem;
                border-bottom-left-radius: 0.5rem;
              }
              &:nth-of-type(2) {
                border-top: 0.2rem solid $primaryColor;
                border-bottom: 0.2rem solid $primaryColor;
              }
              &:nth-of-type(3) {
                border-top: 0.2rem solid $primaryColor;
                border-right: 0.2rem solid $primaryColor;
                border-bottom: 0.2rem solid $primaryColor;
                border-top-right-radius: 0.5rem;
                border-bottom-right-radius: 0.5rem;
              }
              &:hover {
                cursor: pointer;
                background-color: $primaryColor;
                transition: background-color 0.5s, border 0.5s, color 0.5s;
              }
            }
          }
        }
      }

      &-budget {
        @extend .form__label--small;
      }
      &-timing {
        @extend .form__label--small;
        transition: transform 0.5s, color 0.5s;
      }
      &-savings-goal,
      &-investment-goal {
        @extend .form__label--small;
        transition: transform 0.5s, color 0.5s;
      }
    }

    &--extra-small {
      @extend .form__label;
      width: 17.5%;
      left: calc(43.75% + 2rem);
      &-emergency {
        @extend .form__label--extra-small;
        position: relative;
        width: 12.5%;
        justify-content: center;
        transform: translate(0, 0);
        left: 0;
        width: 12.5%;
        border-top: 0.3rem solid $primaryColor;
        border-bottom: 0.3rem solid $primaryColor;
        margin-bottom: 4rem;
        &:first-of-type {
          border-left: 0.3rem solid $primaryColor;
          border-top-left-radius: 0.9rem;
          border-bottom-left-radius: 0.9rem;
        }
        &:nth-of-type(2) {
          border-right: 0.3rem solid $primaryColor;
          border-top-right-radius: 0.9rem;
          border-bottom-right-radius: 0.9rem;
        }
        &:hover {
          cursor: pointer;
        }
      }
    }

    &--comms {
      @extend .form__label;
      position: relative;
      height: 7rem;
      top: 0;
      left: 0;
      transform: translate(10%, 0);
      width: 20%;
