    &--latter-day-saint {
      @include SizeAndPositioning(relative, 6rem, 40%, flex, row, nowrap, flex-start, center, 10%, 0, 0);
      top: 0;
      left: 0;
      transform: translate(10%, 0);
      transition: transform 0.5s;
      &-white {
        @extend .form__label--latter-day-saint;
        width: 15%;
      }
    }

    &--photo-upload {
      @include SizeAndPositioning(relative, 7.5%, max-content, flex, row, nowrap, center, center, 0, 0, 0);
