            border: 0.3rem solid rgba($primaryColor, 0.8);
            border-radius: 1.25rem;
            background-color: rgba($colorOffWhite, 0.8);
            padding: 1rem;
            &:focus {
              outline: none;
            }
          }
          &--open {
            display: flex;
          }
        }
        &__sub-category-container {
          @include SizeAndPositioning(relative, 50rem, 35%, flex, column, nowrap, center, center, 0, 0, 0);
          border: 0.3rem solid $primaryColor;
          border-radius: 1rem;
          &__main-category-display {
            @include SizeAndPositioning(relative, 55%, 100%, flex, row, nowrap, center, center, 0, 0, 0);
            &__left-button,
            &__right-button {
              @include SizeAndPositioning(relative, 100%, 20%, flex, row, nowrap, center, center, 0, 0, 0);
              &__icon {
                &:hover {
                  cursor: pointer;
                  transition: color 0.5s;
                }
              }
            }
            &__category-information {
              @include SizeAndPositioning(relative, 100%, 60%, flex, column, nowrap, center, center, 0, 0, 0);
            }
          }
          &__sub-category-display {
            @include SizeAndPositioning(relative, 45%, 100%, flex, column, nowrap, flex-start, center, 0, 0, 0);
            border-top: 0.3rem solid $primaryColor;
            overflow-y: auto;
            &__sub-category {
              @include SizeAndPositioning(relative, 6rem, 100%, flex, row, nowrap, center, center, 0, 0, 0);
              min-height: 6rem;
              border-bottom: 0.3rem solid rgba($primaryColor, 0.8);
              border-left: none;
              border-right: none;
              background-color: transparent;
              .sub-category-title-container {
                @include SizeAndPositioning(relative, 100%, 70%, flex, row, nowrap, flex-start, center, 0, 0, 0);
                padding-left: 2rem;
              }
              .sub-category-controller {
                @include SizeAndPositioning(relative, 100%, 30%, flex, row, nowrap, space-evenly, center, 0, 0, 0);
                &__surplus-container {
                  @include SizeAndPositioning(relative, 100%, 50%, flex, column, nowrap, center, center, 0, 0, 0);
                  &__switch {
                    @include SizeAndPositioning(relative, 40%, 75%, flex, row, nowrap, flex-start, center, 0, 0, 0);
                    border: 0.2rem solid rgba($primaryColorDark, 0.8);
                    border-radius: 2rem;
                    background-color: rgba($primaryColor, 0.8);
                    transition: border 0.5s, background-color 0.5s, transform 0.5s;
                    &__toggle {
                      @include SizeAndPositioning(relative, 93%, 33%, flex, row, nowrap, center, center, 5%, 0, 0);
                      border: 0.2rem solid rgba($colorOffWhite, 0.8);
                      border-radius: 50%;
                      background-color: rgba($primaryColorDark, 0.8);
                      transition: border 0.5s, background-color 0.5s, transform 0.5s;
                    }
                    &--switched {
                      @include SizeAndPositioning(relative, 40%, 75%, flex, row, nowrap, flex-start, center, 0, 0, 0);
                      border: 0.2rem solid rgba($secondaryColorDark, 0.8);
                      border-radius: 2rem;
                      background-color: rgba($secondaryColor, 0.8);
                      .sub-category-controller__surplus-container__switch__toggle {
                        @include SizeAndPositioning(relative, 93%, 33%, flex, row, nowrap, center, center, 200%, 0, 0);
                        border-radius: 50%;
                        background-color: rgba($secondaryColorDark, 0.8);
                      }
                    }
                    &:hover {
                      cursor: pointer;
                    }
                  }
                }
                &__icon {
                  &:hover {
                    cursor: pointer;
                    transition: color 0.5s;
                  }
                }
              }
            }
            .hidden {
              display: none;
            }
            .category-creation {
              display: none;
              &__input-container {
                @include SizeAndPositioning(relative, 100%, 70%, flex, row, nowrap, flex-start, center, 0, 0, 0);
                padding-left: 2rem;
                &__input {
                  @include SizeAndPositioning(relative, 50%, 50%, flex, row, nowrap, flex-start, center, 0, 0, 0);
                  padding-left: 1rem;
                  border: 0.2rem solid rgba($primaryColor, 0.8);
                  background-color: rgba($colorOffWhite, 0.25);
                  border-radius: 0.5rem;
                  &:focus {
                    border: 0.2rem solid $primaryColor;
                    outline: none;
                  }
                }
              }
              &__controller {
                @include SizeAndPositioning(relative, 100%, 30%, flex, row, nowrap, space-evenly, center, 0, 0, 0);
                &__surplus-container {
                  @include SizeAndPositioning(relative, 100%, 50%, flex, column, nowrap, center, center, 0, 0, 0);
                  &__switch {
                    @include SizeAndPositioning(relative, 40%, 75%, flex, row, nowrap, flex-start, center, 0, 0, 0);
                    border: 0.2rem solid rgba($primaryColorDark, 0.8);
                    border-radius: 2rem;
                    background-color: rgba($primaryColor, 0.8);
                    transition: border 0.5s, background-color 0.5s, transform 0.5s;
                    &__toggle {
                      @include SizeAndPositioning(relative, 93%, 33%, flex, row, nowrap, center, center, 5%, 0, 0);
                      border: 0.2rem solid rgba($colorOffWhite, 0.8);
                      border-radius: 50%;
                      background-color: rgba($primaryColorDark, 0.8);
                      transition: border 0.5s, background-color 0.5s, transform 0.5s;
                    }
                    &--switched {
                      @include SizeAndPositioning(relative, 40%, 75%, flex, row, nowrap, flex-start, center, 0, 0, 0);
                      border: 0.2rem solid rgba($secondaryColorDark, 0.8);
                      border-radius: 2rem;
                      background-color: rgba($secondaryColor, 0.8);
                      .category-creation__controller__surplus-container__switch__toggle {
                        @include SizeAndPositioning(relative, 93%, 33%, flex, row, nowrap, center, center, 0, 0, 0);
                        border-radius: 50%;
                        background-color: rgba($secondaryColorDark, 0.8);
                      }
                    }
                    &:hover {
                      cursor: pointer;
                      @include SizeAndPositioning(relative, 40%, 75%, flex, row, nowrap, flex-start, center, 0, 0, 0);
                      border: 0.2rem solid rgba($secondaryColorDark, 0.8);
                      border-radius: 2rem;
                      background-color: rgba($secondaryColor, 0.8);
                      .category-creation__controller__surplus-container__switch__toggle {
                        @include SizeAndPositioning(relative, 93%, 33%, flex, row, nowrap, center, center, 200%, 0, 0);
                        border-radius: 50%;
                        background-color: rgba($secondaryColorDark, 0.8);
                      }
                    }
                  }
                }
                &__icon {
                  &:hover {
                    cursor: pointer;
                    transition: color 0.5s;
                  }
                }
              }
              &--shown {
                display: flex;
              }
            }
          }
        }

        //////////////////////////////////
        // CREATE BUDGET FORM PAGE FOUR
        &__month-container {
          @include SizeAndPositioning(relative, 62.5rem, 75%, flex, column, nowrap, flex-start, center, 0, 0, 0);
          border: 0.3rem solid $primaryColor;
          border-radius: 1.25rem;
          padding: 0.25rem;
          &__header {
            @include SizeAndPositioning(relative, 6rem, 100%, flex, row, nowrap, center, center, 0, 0, 0);
            background-color: $primaryColor;
            border-top-left-radius: 0.8rem;
            border-top-right-radius: 0.8rem;
            border-bottom: 0.3rem solid $tertiaryColor;
            &__icon {
              @include SizeAndPositioning(absolute, 3rem, 3rem, flex, row, nowrap, center, center, 0, -50%, 0);
              top: 50%;
              left: 1rem;
            }
          }
          &__budget-goal-summary-container {
            @include SizeAndPositioning(relative, 14rem, 100%, flex, row, nowrap, space-between, center, 0, 0, 0);
            .budget-single-goal-summary {
              @include SizeAndPositioning(relative, 100%, 19%, flex, column, nowrap, space-evenly, center, 0, 0, 0);
              background-color: $colorLightestGrey;
              &__title,
              &__amount {
                @include SizeAndPositioning(relative, 50%, 100%, flex, row, nowrap, center, center, 0, 0, 0);
              }
            }
          }
          &__category-container {
            @include SizeAndPositioning(relative, 42.5rem, 100%, flex, row, nowrap, center, center, 0, 0, 0);
            padding: 1.5rem;
            .main-category-display {
              @include SizeAndPositioning(relative, 100%, 40%, flex, row, nowrap, center, center, 0, 0, 0);
              &__button-container {
                @include SizeAndPositioning(relative, 100%, 20%, flex, row, nowrap, center, center, 0, 0, 0);
              }
              &__category-display {
                @include SizeAndPositioning(relative, 100%, 60%, flex, column, nowrap, center, center, 0, 0, 0);
                &__title {
                  margin-top: 2rem;
                }
              }
            }
            .sub-category-display {
              @include SizeAndPositioning(relative, 100%, 60%, flex, column, nowrap, flex-start, center, 0, 0, 0);
              margin-left: 1.5rem;
              overflow-y: auto;
              &__headers {
                @include SizeAndPositioning(relative, 4rem, 100%, flex, row, nowrap, space-between, center, 0, 0, 0);
                min-height: 4rem;
                &__title-container {
                  @include SizeAndPositioning(relative, 100%, 19.5%, flex, row, nowrap, center, center, 0, 0, 0);
                  background-color: $primaryColor;
                }
              }
              &__sub-category {
                @include SizeAndPositioning(relative, 6rem, 100%, flex, row, nowrap, space-evenly, center, 0, 0, 0);
                min-height: 10rem;
                background-color: $colorLightestGrey;
                &__section {
                  @include SizeAndPositioning(relative, 100%, 20%, flex, row, nowrap, center, center, 0, 0, 0);
                  &:first-of-type {
                    justify-content: space-evenly;
                    flex-direction: column;
                    padding: 0.5rem;
                  }
                  &__input {
                    @include SizeAndPositioning(relative, 60%, 80%, flex, row, nowrap, center, center, 0, 0, 0);
                    background-color: rgba($colorOffWhite, 0.35);
                    border-radius: 0.5rem;
                    border: 0.2rem solid rgba($primaryColor, 0.8);
                    caret-color: $primaryColor;

                    &:focus {
                      outline: none;
                      border: 0.2rem solid $primaryColor, 0.8;
                      background-color: $colorOffWhite;
                    }
                  }
                  &__content {
                    @include SizeAndPositioning(relative, 75%, 100%, flex, row, nowrap, center, center, 0, 0, 0);
                  }
                }
                &:hover {
                  cursor: pointer;
                  .sub-category-display__sub-category__section {
                    background-color: $primaryColor;
                    transition: background-color 0.5s;
                    &__input {
                      border: $colorOffWhite;
                      &:focus {
                        outline: none;
                      }
                    }
                    &__content {
                      transition: color 0.5s;
                    }
                  }
                }
                &--hidden {
                  display: none;
                }
              }
              &__timing-container {
                @include SizeAndPositioning(absolute, calc(100% - 4rem), 100%, flex, column, nowrap, flex-start, center, 0, 0, 0);
                background-color: rgba($colorOffWhite, 0.9);
                min-height: calc(100% - 4rem);
                overflow-y: auto;
                top: 4rem;
                left: 0;
                z-index: 10;
                &__close {
                  @include SizeAndPositioning(absolute, 4rem, 4rem, flex, row, nowrap, center, center, 0, 0, 0);
                  top: 2rem;
                  right: 2rem;
                  &:hover {
                    cursor: pointer;
                    transition: color 0.5s;
                  }
                }
                &__monthly-container,
                &__bi-monthly-container,
                &__bi-weekly-container,
                &__weekly-container {
                  @include SizeAndPositioning(relative, min-content, 80%, flex, column, nowrap, center, center, 0, 0, 0);
                  &__label {
                    @include SizeAndPositioning(relative, 5rem, 60%, flex, row, nowrap, space-evenly, center, 0, 0, 0);
                    border: 0.3rem solid rgba($primaryColor, 0.8);
                    border-radius: 0.8rem;
                    padding: 1rem;
                    margin: 0.25rem 0;
                    &:first-of-type {
                      margin-top: 0.5rem;
                    }
                    &:last-of-type {
                      margin-bottom: 0.5rem;
                    }
                    &:focus {
                      border: 0.3rem solid $primaryColor;
                    }
                    &__input,
                    &__select {
                      background-color: rgba($colorOffWhite, 0.25);
                      border: 0.2rem solid rgba($primaryColor, 0.8);
                      border-radius: 0.6rem;
                      padding: 0.2rem;
                      &:focus {
                        border: 0.2rem solid $primaryColor;
                        outline: none;
                      }
                    }
                  }
                  .element-hidden {
                    display: none;
                  }
                }
                &__hidden {
                  display: none;
                }
              }
            }
          }
        }
      }
      .emergency-section {
        flex-direction: row;
        justify-content: center;
        &-header {
          @include SizeAndPositioning(absolute, 6rem, 27.5%, flex, row, nowrap, center, center, 0, 0, 0);
          left: calc(30% + 9.5rem);
          top: 0;
        }
        #lengthOfTime,
        #totalAmount {
          display: none;
          .emergency-checkbox-label,
          & + label {
            @include SizeAndPositioning(relative, 6rem, 12.5%, flex, row, nowrap, center, center, 0, 0, 0);
            left: 0;
            width: 12.5%;
            border-top: 0.3rem solid $primaryColor;
            border-bottom: 0.3rem solid $primaryColor;
            margin-bottom: 4rem;
            &:first-of-type {
              border-left: 0.3rem solid $primaryColor;
              border-top-left-radius: 0.9rem;
              border-bottom-left-radius: 0.9rem;
            }
            &:nth-of-type(2) {
              border-right: 0.3rem solid $primaryColor;
              border-top-right-radius: 0.9rem;
              border-bottom-right-radius: 0.9rem;
            }
            &:hover {
              cursor: pointer;
            }
          }
          & + .clicked-label {
            background-color: $primaryColor;
          }
        }
      }
      .timing-section {
        flex-direction: row;
        justify-content: center;
        #timingNumber {
          width: 12.5%;
          border-radius: 0;
          border-top-left-radius: 0.9rem;
          border-bottom-left-radius: 0.9rem;
          border-right: none;
        }
      }
      .emergency-input {
        &:not(:first-of-type) {
          display: none;
        }
      }
      &-mechanisms {
        @include SizeAndPositioning(relative, 15%, 100%, flex, row, nowrap, center, center, 0, 0, 0);
      }
      .tithing-section {
        display: flex;
        flex-direction: row;
        justify-content: center;
        #grossOption,
        #netOption,
        #surplusOption {
          display: none;
        }
        #grossOptionLabel,
        #netOptionLabel,
        #surplusOptionLabel {
          @include SizeAndPositioning(relative, 7rem, 10%, flex, row, nowrap, center, center, 0, 0, 0);
          top: 0;
          left: 0;
          &:hover {
            cursor: pointer;
            background-color: $primaryColor;
          }
        }
        #grossOptionLabel {
          border-top: 0.3rem solid rgba($primaryColor, 0.8);
          border-left: 0.3rem solid rgba($primaryColor, 0.8);
          border-bottom: 0.3rem solid rgba($primaryColor, 0.8);
          border-top-left-radius: 1.25rem;
          border-bottom-left-radius: 1.25rem;
          &:hover {
            border-top: 0.3rem solid $primaryColor;
            border-left: 0.3rem solid $primaryColor;
            border-bottom: 0.3rem solid $primaryColor;
            transition: border 0.5s, background-color 0.5s, color 0.5s;
          }
        }
        #netOptionLabel {
          border-top: 0.3rem solid rgba($primaryColor, 0.8);
          border-bottom: 0.3rem solid rgba($primaryColor, 0.8);
          &:hover {
            border-top: 0.3rem solid $primaryColor;
            border-bottom: 0.3rem solid $primaryColor;
            transition: border 0.5s, background-color 0.5s, color 0.5s;
          }
        }
        #surplusOptionLabel {
          border-top: 0.3rem solid rgba($primaryColor, 0.8);
          border-right: 0.3rem solid rgba($primaryColor, 0.8);
          border-bottom: 0.3rem solid rgba($primaryColor, 0.8);
          border-top-right-radius: 1.25rem;
          border-bottom-right-radius: 1.25rem;
          &:hover {
            border-top: 0.3rem solid $primaryColor;
            border-right: 0.3rem solid $primaryColor;
            border-bottom: 0.3rem solid $primaryColor;
            transition: border 0.5s, background-color 0.5s, color 0.5s;
          }
        }
        .checked {
          background-color: $primaryColor;
          transition: background-color 0.5s, color 0.5s;
        }
      }
    }

    &__section {
      &__page-display {
        @include SizeAndPositioning(relative, 15%, 100%, flex, row, nowrap, center, center, 0, 0, 0);
      }
    }
  }
}
